.choice {
  padding: 0 85px;
  padding-top: 60px;
  padding-bottom: 60px;
  max-width: 1320px;

  @media (max-width: 1024px) {
    padding: 60px 112px;
  }

  @media (max-width: 960px) {
    padding: 60px 31px 60px;
  }

  @media (max-width: 600px) {
    padding-top: 40px;
    padding-bottom: 50px;
    padding-right: 0;
  }

  @media (max-width: 480px) {
    padding-top: 30px;
    padding-left: 20px;
    padding-bottom: 30px;
  }
}

.choice__title {
  margin: 0;
  margin-bottom: 51px;

  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;

  @media (max-width: 960px) {
    text-align: left;
  }

  @media (max-width: 600px) {
    margin-bottom: 29px;
  }

  @media (max-width: 480px) {
    margin-bottom: 11px;

    font-size: 22px;
    line-height: 23px;
  }
}

.choice__slider {
  //position: relative;
  margin-bottom: 38px;

  @media (max-width: 1023px) and (min-width: 768px) {
    margin-bottom: 28px;
  }

  @media (max-width: 767px) and (min-width: 600px) {
    margin-bottom: 20px;
  }

  @media (max-width: 599px) and (min-width: 320px) {
    margin-bottom: 10px;
  }
}

.choice__slider-btn {
  @media (max-width: 959px) and (min-width: 320px) {
    display: none;
  }
}

.choice__link-block {
  width: fit-content;
  margin: auto;
  margin-top: 38px;

  @media (max-width: 960px) {
    margin-top: 15px;
  }

  @media (max-width: 600px) {
    padding-right: 31px;
    margin-top: 5px;
  }

  @media (max-width: 480px) {
    padding-right: 20px;
    margin-top: 9px;
  }
}
