.events {
  margin: 0 auto;
  padding: 80px 50px 20px;
  width: 100%;
  max-width: 1252px;
  text-align: center;

  p {
    color: $color-default-black;
  }

  @include viewport--lg {
    padding: 40px 30px 0;
  }

  @include viewport--sm {
    padding: 50px 32px 0;
  }

  @include viewport--xs {
    padding: 30px 20px 10px;
  }
}

.events__pagination {
  width: 38.48%;

  @include viewport--lg {
    width: 50%;
  }
}

.events__list {
  @include list-reset;

  margin: 0 0 25px 0;
  width: calc(100% + 28px);
  display: flex;
  flex-wrap: wrap;
  text-align: left;

  @include viewport--lg {
    margin: 0 0 10px 0;
    width: calc(100% + 17px);
  }

  @include viewport--sm {
    width: calc(100% + 19px);
  }

  @include viewport--xs {
    padding: 0 0 10px 0;
    width: 100%;
    justify-content: center;
  }
}

.events__list .main-events-item {
  margin-bottom: 35px;
  width: calc(100% / 4 - 27px);

  span {
    color: $color-melanzane;
  }

  img {
    object-fit: cover;
  }

  @media (max-width: 1023px) {
    margin-right: 16px;
    width: calc(100% / 4 - 16px);
  }

  @include viewport--lg {
    margin-bottom: 28px;
    width: calc(100% / 4 - 16px);
  }

  @include viewport--sm {
    margin-bottom: 30px;
    min-height: 423px;
    font-size: 22px;
    line-height: 30px;
    width: calc(100% / 2 - 19px);

    img {
      margin-bottom: 44px;
      width: 257px;
      height: 184px;
      max-height: 184px;
    }
  }

  @include viewport--xs {
    width: 100%;
    margin-right: 0;
    max-width: 280px;
    min-height: 350px;

    font-size: 22px;
    line-height: 30px;

    img {
      margin-bottom: 37px;
      width: 279px;
      height: 153px;
      max-height: 153px;

    }
  }

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

.events__list .events-card__info-container {
  @include viewport--sm {
    min-height: 190px;
  }

  @include viewport--xs {
    min-height: 160px;
  }
}

.events__list .events-card__info-title {
  @include viewport--xs {
    font-size: 22px;
    line-height: 30px;
  }
}

.events__list .events-card__info {
  color: $color-melanzane;

  @include viewport--xs {
    width: 53%;
  }
}

.events__list .events-card__date,
.events__list .events-card__time {
  color: $color-melanzane;
}

.events__pagination-block {
  padding: 20px 23px 20px 16px;

  width: 100%;
  max-width: 1146px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  @include viewport--sm {
    padding: 44px 10px 15px;
    border-top: 1px solid rgba($color-default-black, 0.1);
  }
}

.events__pagination-buttons {
  width: 25.7%;
  max-width: 285px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include viewport--lg {
    width: 35%;
  }

  @include viewport--sm {
    display: none;
  }
}

.events__showmore {
  margin: 0 0 20px 0;
  width: 176px;
  padding-left: 0;
  padding-right: 0;
}

.events__select {
  width: 60%;
  max-width: 160px;

  @include viewport--lg {
    width: 73.5%;
    max-width: 210px;
  }
}

