.dropdown {
  position: relative;

  ul {
    margin: 0;
    padding: 25px 0 0 0;

    list-style: none;
  }
}

.dropdown__list {
  position: absolute;
  top: 62px;
  left: 0;
  z-index: 1;

  display: none;
  flex-direction: column;
  width: 230px;
  margin: 0;
  padding: 0;

  background-color: $color-default-white;
  list-style: none;

  &--opened {
    display: flex;
    flex-direction: column;
  }
}

.dropdown__item {
  width: 100%;
}

.dropdown__link {
  display: inline-block;
  width: 100%;
  padding: 9px 30px;

  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  color: $color-default-black;
  word-break: break-word;

  &:hover,
  &:focus {
    background-color: $color-white-smoke;
  }
}

.dropdown__bottom {
  margin-top: 30px;
  padding: 15px 30px 15px;

  background-color: $color-white-smoke;

  p {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0;
    padding: 0;
  }

  span {
    font-size: 16px;
    line-height: 22px;
    opacity: 0.3;
  }
}

.dropdown__bottom-link {
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;

  span {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: $color-default-black;
    word-break: break-word;

    opacity: 1;
  }

  &:hover span,
  &:focus span {
    color: $color-gold;
  }


  &:hover svg,
  &:focus svg {
    fill: $color-gold;
  }
}

.dropdown__toggle {
  outline: none;
  stroke: $color-default-white;
}
