.btn {
  display: inline-block;
  padding: 14px 64px 11px;
  position: relative;

  font-size: 18px;
  line-height: 25px;
  font-weight: 400;
  color: $color-default-white;
  vertical-align: middle;
  text-align: center;
  word-break: break-word;

  background-color: $color-gold;
  border-radius: 5px;
  border: 0;
  outline: none;
  cursor: pointer;

  svg {
    position: absolute;
    left: 17px;
    top: 50%;
    transform: translateY(-50%);
    fill: $color-default-white;
  }

  &--confirm {
    max-width: 165px;
    padding-right: 44px;
    padding-left: 45px;
  }

  &--outlined {
    color: $color-black;
    border: 1px solid $color-silver;
    background-color: $color-transparent;
    transition: $default-transition-settings;

    svg {
      fill: $color-gold;
    }
  }

  &--white-text {
    color: $color-white-smoke;
  }

  &:hover,
  &:focus {
    background-color: $color-active-gold;
    border-color: $color-transparent;

    color: $color-default-white;

    svg {
      fill: $color-default-white;
    }
  }

  &:active {
    background-color: $color-active-gold;
  }

  &--disabled,
  &:disabled {
    background-color: $color-gainsboro;
    border-color: $color-transparent;

    color: $color-default-white;

    cursor: auto;

    svg {
      fill: $color-default-white;
    }

    &:hover,
    &:focus,
    &:active {
      background-color: $color-gainsboro;
      border-color: $color-transparent;

      color: $color-default-white;
    }
  }

  &--capitalize {
    text-transform: capitalize;
  }

  &--big {
    padding: 8px 49px;
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
  }

  &--medium {
    padding: 8px 40px;
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
  }

  &--small {
    min-height: 30px;
    padding: 3px 10px;
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
  }

  &--grey {
    padding: 8px 40px;

    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-black;

    background-color: $color-gainsboro-2;

    &:disabled {
      color: $color-default-black;

      &:hover,
      &:focus {
        color: $color-default-black;
        background-color: $color-gainsboro-2;
      }
    }
  }

  &--card {
    padding: 3px 12px 4px;

    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
  }

  &--pagination-nav {
    padding: 0;
    width: 71px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    color: $color-zambezi;
    background-color: transparent;

    &:hover,
    &:focus {
      background-color: $color-transparent;
      color: $color-black-russian;
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }

    span {
      display: block;
      width: 52px;
    }

    svg {
      position: initial;
      top: 0;
      left: 0;
    }
  }

  &--pagination {
    padding: 4px 11px;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    border-radius: 60px;
    color: $color-default-white;

    &:hover,
    &:focus {
      background-color: $color-gold;
    }

    &:active {
      background-color: $color-active-gold;
    }
  }

  &--pagination-inactive {
    background-color: $color-transparent;
    color: $color-black-russian;
  }
}

