.filter-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: -13px;
  margin-right: -13px;

  @media (max-width: 480px) {
    margin-top: -5px;
    margin-right: -5px;
  }
}

.filter-input {
  margin-right: 13px;
  margin-top: 13px;

  input {
    &:focus {
      & ~ label {
        border: 1px solid $color-gray-1;
      }
    }

    &:checked {
      & ~ label {
        &::after {
          transform: translateY(-50%) rotate(180deg);
        }
      }
    }
  }

  label {
    position: relative;

    display: block;
    padding: 9px 34px 10px 14px;

    font-size: 14px;
    line-height: 14px;

    border: 1px solid $color-silver;
    border-radius: 3px;
    color: $color-gray-1;

    font-family: $font-family--myseo;
    cursor: pointer;
    transition: opacity $default-transition-settings, border $default-transition-settings;
    user-select: none;

    &:hover {
      border: 1px solid $color-gray-1;
    }

    &::after {
      content: "";
      position: absolute;
      right: 12px;
      width: 10px;
      height: 5px;

      top: 50%;
      transform: translateY(-50%);
      transition: transform $default-transition-settings;
      background-image: url("../img/sprite/icon-triangle.svg");
    }
  }

  @media (max-width: 800px) {
    label {
      padding: 17px 37px 17px 15px;
    }
  }

  @media (max-width: 480px) {
    margin-right: 5px;
    margin-top: 5px;

    label {
      padding: 3px 18px 3px 4px;

      font-size: 10px;
      line-height: 14px;

      &::after {
        right: 4px;
      }
    }
  }
}
