.main-products {
  padding-top: 60px;
  padding-bottom: 46px;
  background-color: $color-ivory;

  .product {
    width: calc(100% / 4 - 16px);
    margin-top: 16px;
    margin-bottom: 16px;
    margin-right: 16px;

    @media (max-width: 960px) {
      width: 245px;
      margin-right: 16px;

      &:first-child {
        margin-left: 31px;
      }

      &:last-child {
        margin-right: 31px;
      }
    }

    @media (max-width: 600px) {
      width: 230px;
    }

    @media (max-width: 480px) {
      width: 233px;
      margin-right: 9px;
    }
  }

  @media (max-width: 1024px) {
    padding-bottom: 44px;

    .container {
      padding: 0 30px;
    }
  }

  @media (max-width: 960px) {
    padding-bottom: 36px;
  }

  @media (max-width: 600px) {
    padding-top: 39px;
    padding-bottom: 25px;
  }

  @media (max-width: 480px) {
    padding-top: 30px;
    padding-bottom: 16px;

    .container {
      padding: 0 20px;
    }
  }
}

.main-products__title {
  margin: 0 0 52px;

  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;

  @media (max-width: 1024px) {
    margin-bottom: 51px;
  }

  @media (max-width: 960px) {
    margin-bottom: 30px;
  }

  @media (max-width: 600px) {
    margin-bottom: 21px;
    text-align: left;
    font-size: 36px;
    line-height: 105%;
  }

  @media (max-width: 480px) {
    margin-bottom: 18px;
    font-size: 22px;
    line-height: 105%;
  }
}

.product-big {
  position: relative;
  margin-left: auto;
  margin-bottom: 30px;

  transition: box-shadow $default-transition-settings;

  &:hover {
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15), 0 6px 15px rgba(0, 0, 0, 0.15);
  }

  @media (max-width: 1024px) {
    margin-bottom: 50px;
  }

  @media (max-width: 960px) {
    margin-bottom: 14px;
  }

  @media (max-width: 480px) {
    margin-bottom: 4px;
  }
}

.product-big__img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 808px;
  height: 100%;
  width: 73%;
  z-index: 0;

  @media (max-width: 600px) {
    width: 100%;
    position: static;
    height: 335px;
  }

  @media (max-width: 480px) {
    height: 200px;
  }

  img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.product-big__info {
  margin-left: auto;
  min-height: 335px;
  display: flex;
  flex-direction: column;
  width: 455px;
  justify-content: space-between;

  padding: 26px 26px 26px 117px;

  clip-path: polygon(26% 0, 100% 0, 100% 100%, 16% 100%, 0 6%);


  color: $color-default-white;
  background-color: $color-melanzane;
  z-index: 3;

  @media (max-width: 880px) {
    width: 370px;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-left: 0;
    min-height: unset;
    padding: 22px 30px 24px 30px;

    clip-path: none;
  }

  @media (max-width: 480px) {
    padding: 21px 19px 19px;
  }
}

.product-big__info-top {
  margin-bottom: 30px;

  @media (max-width: 480px) {
    margin-bottom: 11px;
  }
}

.product-big__info-title {
  margin: 0;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
}

.product-big__info-art {
  display: block;
  margin: 0 0 31px;

  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;


  @media (max-width: 480px) {
    margin: 0 0 18px;
  }
}

.product-big__info-text {
  width: 75.9%;
  margin: 0;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;

  @media (max-width: 480px) {
    width: 86.9%;
  }
}

.product-big__info-bottom {
  //display: flex;
  //justify-content: space-between;
  //flex-direction: column;

  button {
    color: $color-default-white;
    border: 1px solid white;

    @media (max-width: 600px) {
      width: 151px;
    }
  }

  @media (max-width: 600px) {
    display: flex;
    justify-content: space-between;
    //flex-direction: row;
  }

  @media (max-width: 480px) {
    flex-direction: column;
  }
}

.product-big__info-cost {
  display: block;
  margin: 0 0 13px;

  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;

  @media (max-width: 600px) {
    margin: 0;
    align-self: center;
  }

  @media (max-width: 480px) {
    margin: 0 0 19px;
    align-self: start;
  }
}

.product-big__info-btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  //margin: auto;
  min-width: 104px;
  min-height: 30px;
  padding: 3px 0;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  vertical-align: middle;
  text-align: center;
  color: $color-default-white;
  word-wrap: break-word;
  //word-break: normal; /* для Opera и FireFox */
  /* stylelint-disable */
  //word-break: break-word; /* для Chrome */
  /* stylelint-enable */

  background-color: transparent;
  border: 1px solid $color-silver;
  border-radius: 3px;

  &:hover,
  &:focus {
    color: $color-default-white;
    outline: none;
    cursor: pointer;
    background-color: $color-fallow;

    transition: $default-transition-settings;
  }

  &:not(:hover) {
    transition: $default-transition-settings;
  }

  @media (max-width: 767px) {
    min-width: 151px;
    max-height: 25px;
  }
}

.main-products__list-wrapper {
  @media (max-width: 960px) {
    overflow: auto;
    width: calc(100% + 62px);
    margin-left: -31px;
  }
}

.main-products__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  margin-right: -16px;
  margin-top: -16px;

  @media (max-width: 960px) {
    margin: 0;
    width: max-content;
    flex-wrap: nowrap;
  }
}

.product {
  display: flex;
  flex-shrink: 0;
  min-height: 335px;
  width: 229px;

  background-color: $color-default-white;
  border: 1px solid $color-gainsboro;
  transition: box-shadow $default-transition-settings;

  img {
    object-fit: cover;
  }

  &.big {
    width: calc((100% / 4) * 3 - 16px);
    padding: 8px;

    .product__link {
      padding: 28px 19px 6px;
    }

    img {
      width: 65.8%;
    }

    .product__text {
      font-weight: normal;
      font-size: 16px;
      line-height: 22px;
      margin: 0;
      margin-top: 30px;

      color: $color-matterhorn;
    }

    @media (max-width: 1024px) {
      img {
        width: 59.3%;
      }
    }

    @media (max-width: 960px) {
      img {
        width: 56.2%;
      }
    }

    @media (max-width: 600px) {
      width: calc(100% - 16px);

      img {
        width: 45.2%;
        height: 100%;
      }

      .product__link {
        padding: 40px 19px 9px;
      }

      .product__text {
        margin: 19px 0 0;
      }
    }

    @media (max-width: 480px) {
      width: 100%;

      .product__text {
        margin: 19px 0 19px;
      }
    }
  }

  &:hover {
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15), 0 6px 15px rgba(0, 0, 0, 0.15);
  }
}

.main-products__block-top {
  margin-bottom: 39px;

  @media (max-width: 1439px) and (min-width: 1024px) {
    margin-bottom: 41px;
  }
}

.product__link {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  padding: 9px 9px 13px;

  img {
    margin-bottom: 7px;
    max-width: 100%;
    width: 100%;
    height: 155px;
    object-fit: cover;
  }

  h3 {
    margin: 0;

    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
  }
}

.product__art {
  display: block;

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  color: $color-silver;
}

.product__cost {
  display: block;
  margin-bottom: 13px;

  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
}
