.event {
  padding: 26px 0 0 0;

  background-color: $color-black-2;

  @include viewport--lg {
    padding: 26px 0 10px;
  }

  @include viewport--sm {
    padding: 40px 0 5px;
  }

  @include viewport--xs {
    padding: 25px 0 0;
  }
}

.event__wrapper {
  max-width: 1440px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.event__navigation {
  position: relative;
  padding: 0 50px;

  width: 100%;

  @include viewport--sm {
    padding: 0 30px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  @include viewport--xs {
    padding: 0 20px;
  }
}

.event__title {
  h1 {
    margin: 0 0 22px 0;
    width: 100%;
    max-width: 670px;

    color: $color-active-gold;
    text-align: center;
  }

  @include viewport--sm {
    h1 {
      margin: 0 0 40px 0;
      text-align: left;
    }
  }

  @include viewport--xs {
    h1 {
      margin: 0 0 18px 0;
    }
  }
}

.event__link-block {
  position: absolute;
  top: 4px;
  left: 50px;

  @include viewport--lg {
    top: 4px;
    left: 30px;
  }

  @include viewport--sm {
    position: initial;

    margin: 0 0 35px 0;
  }
}

.event__link-more {
  width: 100%;
  max-width: 164px;

  display: flex;
  color: $color-default-white;

  svg {
    margin: 0 11px 0 0;
    order: -1;
    transform: rotate(180deg);
    fill: $color-default-white;
  }

  @include viewport--sm {
    justify-content: space-between;
    max-width: 144px;

    svg {
      margin: 0;
    }
  }

  @include viewport--xs {
    span {
      display: none;
    }
  }
}

.event__breadcrumbs {
  margin: 0 auto;

  flex-grow: 2;

  a {
    color: $color-default-white;
    transition: 0.3s ease;

    &:hover,
    &:focus {
      text-decoration: underline;
      color: $color-suva-grey;
    }

    &:active {
      text-decoration: none;
      color: $color-suva-grey;
    }
  }

  @include viewport--sm {
    margin: 0;
  }
}

.event__info {
  position: relative;

  width: 100%;

  @include viewport--sm {
    flex-direction: column;
  }
}

.event__picture {
  position: absolute;

  top: -20px;
  right: calc(49% + 20px);

  z-index: 1;

  width: 566px;

  img {
    clip-path: polygon(0 384px, 462px 495px, 566px 188px, 122px 0);
  }

  @include viewport--lg {
    top: -8px;
    right: calc(49.2% + 16px);

    width: 442px;

    img {
      width: 442px;
      height: 384px;

      clip-path: polygon(360px 382px, 441px 146px, 96px 0, 0 307px);
    }
  }

  @include viewport--sm {
    top: 0;
    right: 0;
    left: 0;

    width: 538px;

    img {
      width: 538px;
      height: 467px;

      clip-path: polygon(441px 466px, 538px 178px, 120px 0, 0 373px);
    }
  }

  @include viewport--xs {
    width: 280px;

    img {
      width: 280px;
      height: 243px;

      clip-path: polygon(229px 243px, 282px 95px, 60px 0, 0 193px);
    }
  }
}

.event__text {
  margin: 0 0 0 auto;
  padding: 75px 0 0 0;
  width: 49.38%;
  max-width: 563px;

  h2 {
    margin: 0 0 26px 0;
    padding: 0;

    color: $color-default-white;
    font-weight: 400;

    span {
      margin: 0 2.8% 0 0;
    }
  }

  p {
    margin: 0 0 48px 0;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;

    &:last-of-type {
      margin: 0;
    }
  }

  @include viewport--lg {
    width: 49%;

    p {
      margin: 0 0 25px 0;
      width: 93%;
    }
  }

  @include viewport--sm {
    padding: 505px 0 0 0;

    width: 100%;

    h2 {
      margin: 0 0 25px 0;
    }

    p {
      margin: 0 0 25px 0;
      width: 75%;
    }
  }

  @include viewport--xs {
    padding: 272px 0 0 0;

    h2 {
      margin: 0 0 20px 0;

      span {
        margin: 0 5% 0 0;
      }
    }

    p {
      margin: 0 0 20px 0;
      width: 100%;
    }
  }
}
