.restaurants-promo {
  display: flex;
  padding: 7px 4px 9px 8px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 1px solid $color-gainsboro;

  @include viewport--lg {
    padding: 11px 4px 11px 8px;
  }
}

.restaurants-promo__image {
  align-self: center;
}

.restaurants-promo img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.restaurants-promo__description {
  display: flex;
  flex-direction: column;
  max-width: 34%;
  padding: 28px 15px 19px 20px;
  box-sizing: border-box;

  @include viewport--lg {
    max-width: 44%;
    padding-top: 38px;
  }

  @include viewport--sav {
    max-width: 55%;
  }
}

.restaurants-promo__description h2 {
  margin: 0 0 40px;
  text-transform: none;
  color: $color-gray-1;

  @include viewport--lg {
    margin: 0 0 26px;
  }

  @include viewport--sav {
    margin: 0 0 28px;
  }
}

.restaurants-promo__description-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
}

.restaurants-promo__description-wrapper p {
  margin: 0;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
}

.restaurants-promo__category {
  display: flex;
  flex-wrap: wrap;
}

.restaurants-promo__category p {
  padding-right: 9px;
  margin-top: 10px;
  margin-right: 13px;
  line-height: 16px;
  border-right: 1px solid $color-default-black;
}

.restaurants-promo__category p:last-child {
  padding-right: 0;
  border: none;
}
