.form-agreement {
  position: relative;
  margin: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: $color-suva-grey;
}

.form-agreement input + label {
  display: flex;
  user-select: none;
  padding-left: 30px;
  padding-right: 20px;
  cursor: pointer;
}

.form-agreement label > span {
  display: block;
  margin: 0;
}

.form-agreement input + label::before {
  position: absolute;
  content: "";
  top: 5px;
  left: 0;
  width: 18px;
  height: 18px;
  border: 1px solid $color-suva-grey;
}

.form-agreement input:not(:checked) + label svg {
  display: none;
}

.form-agreement input:checked + label svg {
  position: absolute;
  top: 8px;
  left: 2px;
  display: block;
}

.form-agreement input:disabled + label::before {
  background-color: $color-gainsboro-2;
}

.form-agreement input + label:hover::before,
.form-agreement input:focus + label::before {
  border: 1px solid $color-light-grey;
  cursor: pointer;
}

.form-agreement a {
  color: $color-default-white;
  background: linear-gradient(to right, $color-default-white) left bottom 0 no-repeat;
  background-size: 0 1px;

  &:hover,
  &:focus {
    outline: none;
    background-size: 100% 1px;
    cursor: pointer;
  }

  &:active {
    color: $color-suva-grey;
  }
}

.form-agreement > input.invalid + label::before {
  border: 1px solid $color-torch-red;
}

