.promo {
  overflow: hidden;
  padding-top: 157px;
  padding-bottom: 209px;

  background-color: $color-melanzane;

  .container {
    position: relative;
  }

  @media (max-width: 1024px) {
    padding-top: 95px;
    padding-bottom: 53px;

    .container {
      padding: 0 30px;
    }
  }

  @media (max-width: 960px) {
    padding-top: 60px;
    padding-bottom: 33px;
  }

  @media (max-width: 820px) {
    padding-top: 43px;
    padding-bottom: 435px;
  }

  @media (max-width: 480px) {
    padding-top: 237px;
    padding-bottom: 40px;

    .container {
      padding: 0 20px;
    }
  }
}

.promo__arrows {
  .navigation-arrow--prev {
    margin-right: 12px;
  }

  @media (max-width: 1024px) {
    .navigation-arrow--prev {
      margin-right: 7px;
    }
  }
}

.promo__text-wrapper {
  width: 500px;

  h1 {
    margin: 0;
    margin-bottom: 20px;

    font-style: normal;
    font-weight: 400;
    font-size: 54px;
    line-height: 48px;
    color: $color-fallow;
  }

  p {
    width: 100%;
    max-width: 479px;
    margin: 0 0 27px;

    font-size: 20px;
    line-height: 27.5px;
    color: $color-default-white;
  }

  @media (max-width: 1024px) {
    width: 365px;
    min-height: 361px;

    p {
      padding-right: 15px;
    }
  }

  @media (max-width: 820px) {
    width: 78%;
    min-height: unset;

    p {
      margin-bottom: 17px;
    }
  }

  @media (max-width: 480px) {
    width: 100%;

    h1 {
      font-size: 28px;
      line-height: 25px;
    }

    p {
      margin-bottom: 29px;

      font-size: 16px;
      line-height: 22px;
    }
  }
}

.promo__block {
  width: 100%;
  position: relative;
}

.promo__image {
  position: absolute;
  width: 753px;
  height: 640px;
  top: -153px;
  left: 585px;
  z-index: -1;

  img {
    position: relative;
    left: 0;
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }

  @media (max-width: 1024px) {
    width: 531px;
    height: 455px;
    top: -66px;
    left: 489px;
  }

  @media (max-width: 960px) {
    top: -46px;
    left: 490px;
    width: 461px;
    height: 393px;
  }

  @media (max-width: 820px) {
    width: 614px;
    height: 523px;
    top: unset;
    bottom: -415px;
    left: unset;
    right: -124px;
  }

  @media (max-width: 480px) {
    width: 320px;
    height: 211px;
    top: -241px;
    bottom: unset;
    right: 0;
  }
}
