.about-features {
  padding: 60px 0 50px;
  background-color: $color-black;

  @include viewport--lg {
    padding: 40px 0 45px;
  }

  @include viewport--sm {
    padding: 42px 0 20px 0;
  }

  @include viewport--xs {
    padding: 30px 0 0 0;
  }
}

.about-features__wrapper {
  position: relative;
  padding-bottom: 52px;

  @include viewport--lg {
    padding-bottom: 75px;
  }

  @include viewport--sm {
    padding-bottom: 10px;
  }

  @include viewport--xs {
    padding-bottom: 0;
  }
}

.about-features__description {
  margin: 0 0 80px 0;
  max-width: 796px;
  width: 100%;

  p {
    margin: 0;
    padding: 0;

    font-size: 18px;
    line-height: 24.6px;

    color: $color-default-white;

    &:first-of-type {
      margin: 0 0 23px 0;
      padding: 0;

      width: 100%;

      font-weight: 400;
      font-size: 36px;
      line-height: 37.8px;

      color: $color-active-gold;
      text-transform: uppercase;
    }
  }

  @include viewport--lg {
    margin: 0 0 43px 0;
    width: 87%;
  }

  @include viewport--sm {
    margin: 0 0 35px 0;

    p {
      font-size: 16px;
      line-height: 21.86px;
      width: 98%;

      &:first-of-type {
        margin: 0 0 25px 0;
      }
    }
  }

  @include viewport--xs {
    margin: 0 0 82px 0;
    width: 100%;

    p {
      width: 100%;

      font-size: 16px;
      line-height: 21.86px;

      &:first-of-type {
        margin: 0 0 23px 0;
        font-size: 22px;
        line-height: 23.1px;
      }
    }
  }
}

.about-features__text {
  position: absolute;
  bottom: 30px;
  left: 220px;
  z-index: 1;

  width: 33.7%;
  display: flex;
  align-items: center;

  p {
    margin: 0;
    padding: 0;

    width: 65%;

    font-size: 20px;
    line-height: 27.3px;

    color: $color-default-white;
    text-transform: uppercase;
  }

  @include viewport--lg {
    width: 50%;

    bottom: 20px;
    left: 210px;

    p {
      width: 62%;
    }
  }

  @include viewport--sm {
    width: 90%;

    bottom: 95px;
    left: 30px;

    flex-wrap: wrap;
    justify-content: center;

    p {
      width: 50%;
    }
  }

  @include viewport--xs {
    display: none;
  }
}

.about-features__places {
  width: 27%;

  @include viewport--sm {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.about-features__places-num {
  position: relative;

  width: 110px;

  margin: 3px 0 0 4%;

  font-weight: 100;
  font-size: 70px;
  line-height: 90px;

  color: $color-default-white;

  &::after {
    position: absolute;
    top: 15px;
    right: -60px;
    content: "";

    width: 47px;
    height: 1px;

    background-color: $color-default-white;
    transform: rotate(135deg);
  }

  @include viewport--lg {
    width: 125px;

    &::after {
      right: -45px;
    }
  }

  @include viewport--sm {
    width: 118px;

    &::after {
      right: -47px;
    }
  }
}

.about-features__places-descr {
  margin: -7px 0 0 5px;
  width: 70%;

  font-size: 14px;
  line-height: 19.2px;

  color: $color-default-white;
  text-transform: uppercase;

  @include viewport--md {
    margin: -7px 0 0 0;
  }

  @include viewport--sm {
    padding: 0 10px 0 0;
    width: 42%;
  }
}
