.form-search__field {
  position: relative;

  width: 100%;

  border-radius: 30px;
}

.form-search__field input {
  width: 100%;
  padding: 5px 45px 5px 23px;

  color: $color-default-black;
  font-size: 16px;
  line-height: 15px;

  border-radius: 40px;
  border: 1px solid $color-transparent;
  background-color: $color-default-white;
  outline: none;

  &::placeholder {
    color: $color-default-black;

    opacity: 0.3;
  }

  &.error {
    border-color: $color-torch-red;
  }
}

.form-search__field button {
  position: absolute;
  top: 50%;
  right: 17.5px;

  width: 25px;
  height: 25px;
  padding: 0;
  margin-top: -12.5px;

  border: 0;
  background-color: $color-transparent;
  cursor: pointer;
  outline: none;

  svg {
    fill: $color-default-black;
    opacity: 0.3;
  }

  &:hover svg,
  &:focus svg {
    opacity: 1;
  }
}
