.about-order {
  margin-bottom: 81px;
  padding-top: 30px;

  @include viewport--xs {
    margin-bottom: 30px;
  }
}

.about-order .container {
  max-width: 778px;
}

.about-order .inner-header {
  margin-bottom: 42px;

  @include viewport--xs {
    margin-bottom: 20px;
  }
}

.about-order__text {
  margin-bottom: 78px;

  @include viewport--xs {
    margin-bottom: 30px;
  }
}

.about-order__text p {
  margin: 0;

  font-style: normal;
  font-size: 16px;
  line-height: 22px;
  color: $color-eclipse;
}

.about-order__info .h2 {
  margin: 0 0 7px 0;

  @include viewport--xs {
    margin-bottom: 11px;

    font-size: 19px;
    line-height: 26px;
  }
}

.about-order__payment-way h2 {
  margin: 0 0 5px 0;

  color: $color-gray-1;

  @include viewport--xs {
    font-size: 19px;
    line-height: 26px;
  }
}

.about-order__bottom {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: 620px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.about-order__back-block {
  display: flex;
  justify-self: flex-start;
  margin-right: 28px;

  @media (max-width: 620px) {
    margin-bottom: 40px;
  }
}

.about-order__back-more {
  flex-direction: row-reverse;

  span {
    margin-left: 13px;
  }

  svg {
    margin-left: 0;

    transform: rotate(180deg);
  }
}

.about-order__btn-event {
  padding: 8px 16px;

  text-transform: uppercase;

  @include viewport--xs {
    width: 100%;
  }
}
