.h1 {
  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 38px;

  @include viewport--xs {
    font-size: 22px;
    line-height: 23px;
  }
}

.h2 {
  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
}

.h2-normal {
  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 30px;
}

.h3 {
  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
}

.h4 {
  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
}
