.main-product__link {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  padding: 7px 9px 15px;
  border: 1px solid $color-gainsboro;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  h3 {
    margin: 0;
    font-family: $font-family--avenir;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
  }
}

.main-product__block-description {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
  max-width: 100%;
  padding-top: 7px;
}

.main-product__art {
  display: block;

  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  color: $color-silver;
}

.main-product__cost {
  display: block;
  margin-bottom: 13px;

  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
}

.main-product__text {
  font-family: $font-family--avenir;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
}

.main-product__text p {
  margin: 30px 0 10px;
  max-width: 250px;
}
