.inner-header {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include viewport--sm {
    align-items: flex-start;
  }

  h1 {
    margin: 0 0 5px 0;

    color: $color-active-gold;
    text-align: center;
    text-transform: uppercase;

    @include viewport--sm {
      text-align: left;
    }

    @include viewport--xs {
      margin-bottom: 10px;

      font-size: 22px;
      font-weight: 400;
      line-height: 23px;
    }
  }
}

.inner-header__subtitle {
  display: inline-block;
  width: 100%;
  margin: 0;
  padding: 0;

  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  color: $color-default-black;
  vertical-align: top;

  @include viewport--sm {
    text-align: left;
  }

  @include viewport--xs {
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
  }
}
