.personal {
  margin: 30px 0;
}

.personal .container {
  max-width: 860px;
}

.personal .inner-header {
  margin-bottom: 55px;

  @include viewport--xs {
    margin-bottom: 10px;
  }
}

.personal .inner-menu {
  margin-bottom: 19px;
  padding-top: 0;

  background-color: $color-transparent;

  @include viewport--sm {
    margin-bottom: 9px;
  }
}

.personal .inner-menu__list {
  grid-template-columns: repeat(4, 1fr);

  @include viewport--lg {
    width: 100%;
  }

  @media (max-width: 830px) {
    grid-auto-columns: 185px;
    grid-auto-flow: column;
    grid-template-columns: repeat(4, minmax(185px, 1fr));
  }

  @include viewport--xs {
    grid-auto-columns: 150px;
    grid-template-columns: repeat(4, minmax(150px, 1fr));
  }
}

.personal .inner-menu__item {
  width: auto;
}

.personal .inner-menu__link {
  padding: 16px 8px;

  @include viewport--xs {
    padding: 7px 8px 8px;
  }
}
