.advantages {
  @media (max-width: 1439px) and (min-width: 1024px) {
    padding: 0 30px;
  }
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  list-style: none;
  margin: 0;
  margin-right: -19px;
  padding: 32px 0 0;
  border-top: 1px solid $color-fallow;

  @media (max-width: 1024px) {
    padding-top: 26px;
  }

  @media (max-width: 600px) {
    padding-top: 4px;
  }

  @media (max-width: 480px) {
    padding-top: 30px;
    flex-direction: column;
  }
}

.advantages__item {
  display: flex;
  width: calc(100% / 4 - 15px);
  margin-right: 19px;
  margin-top: 19px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  svg {
    fill: $color-transparent;
  }

  p {
    width: calc(100% - 80px);
    margin: 10px 0 0 7.2%;
    font-family: "AvenirNext", "Verdana", serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: $color-default-white;

    @media (max-width: 1024px) {
      width: 100%;
      margin: 23px 0 0 0;
    }

    @media (max-width: 600px) {
      margin: 3px 0 35px 13.2%;
      align-self: center;
    }

    @media (max-width: 480px) {
      align-self: center;
      width: 83%;
      margin: 0;
    }
  }

  @media (max-width: 1024px) {
    flex-direction: column;
    justify-content: flex-start;
    width: 23.5%;
    margin-right: 2%;
  }

  @media (max-width: 600px) {
    width: calc(100% / 2 - 15px);
    margin-right: 23px;
    margin-top: 34px;
    flex-direction: row;

    &:nth-child(4n) {
      margin-right: 23px;
    }

    &:nth-child(2n) {
      margin-right: 0;
    }

    p {
      margin: 0;
      width: 100%;
      padding-top: 6px;
    }

    svg {
      flex-shrink: 0;
      margin-right: 32px;
    }
  }

  @media (max-width: 480px) {
    width: 100%;
    margin-top: 0;
    margin-bottom: 31px;

    &:last-child {
      margin-bottom: 0;
    }

    p {
      padding-top: 0;
    }

    svg {
      margin-right: 11px;
      width: 38px;
      height: 38px;
    }
  }
}

