.portions {
  display: flex;

  p {
    margin: 0;
    margin-left: 5px;
    padding-top: 5px;

    font-weight: normal;
    font-size: 14px;
    line-height: 19px;

    color: $color-bunting;
  }

  & > div {
    display: flex;
    margin-right: 23px;

    &:last-child {
      margin-right: 0;
    }
  }
}
