.meal-cards {
  margin-top: 36px;
  padding-right: 34px;

  @include viewport--lg {
    margin-top: 25px;
  }

  @include viewport--sm {
    margin-top: 40px;
  }

  @include viewport--xs {
    margin-top: 24px;
  }
}

.meal-cards__back-block {
  display: none;

  @include viewport--sm {
    display: block;
    margin-bottom: 35px;
  }
}

.meal-cards__back-block a {
  display: flex;
}

.meal-cards__back-block svg {
  order: -1;
  transform: rotate(180deg);
  margin-left: 0;
  margin-right: 13px;
}

.meal-cards__back-block span {
  @include viewport--xs {
    display: none;
  }
}