.columns-list {
  border: 1px solid $color-gold;
  padding: 29px 28px 37px 28px;

  ul {
    @include list-reset;

    columns: 2;

    li {
      margin-bottom: 10px;
      break-inside: avoid;

      font-weight: 500;
      font-size: 16px;
      line-height: 22px;

      color: black;
    }
  }
}

.columns-list__header {
  margin: 0;
  margin-bottom: 25px;

  font-weight: 600;
  font-size: 18px;
  line-height: 25px;

  color: black;
}
