.recipes-slider__container {
  position: relative;

  .swiper-pagination {
    position: absolute;
    top: 376px;
    left: 468px;
    max-width: 320px;
    flex-wrap: wrap;
    display: flex;
  }

  .swiper-pagination-bullet {
    margin: 0;
    position: relative;
    display: block;
    width: 10px;
    height: 10px;
    border: 1px solid $color-melanzane;
    border-radius: 50%;
    opacity: 0.8;
    background-color: transparent;

    &-active {
      width: 40px;
      height: 10px;
      border: none;
      background-color: $color-apache;
      border-radius: 10px;
    }
  }

  & > .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0;
    margin-top: 20px;
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    width: 86px;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 52.08%, white 91.67%);
  }

  @media (max-width: 600px) {
    &::after {
      display: none;
    }

    .swiper-pagination {
      display: none;
    }
  }
}

.recipes-slider {
  @media (max-width: 600px) {
    padding: 0 30px;
    width: max-content;
  }

  @media (max-width: 480px) {
    padding: 0 22px;
  }
}

.recipes-slider__list {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;

  li:last-child {
    margin-right: 0;
  }
}

.recipes-slider__item {
  display: flex;
  flex-direction: column;
  width: 283px;

  text-align: left;
  border: 1px solid $color-gainsboro;
  transition: width $default-transition-settings, height $default-transition-settings, opacity $default-transition-settings;

  &.swiper-slide-active {
    width: 450px;
    border: 0;

    @media (max-width: 960px) {
      width: 438px;
    }

    .recipes-slider__info {
      width: 300px;
      padding: 35px 3px 10px 3px;
    }

    h3 {
      font-weight: 600;
      font-size: 22px;
      line-height: 30px;
    }

    p {
      font-size: 14px;
      line-height: 19px;
    }

    img {
      height: 406px;
      width: 100%;
      object-fit: cover;
    }
  }

  h3 {
    margin: 0 0 13px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    transition: $default-transition-settings;
  }

  p {
    margin: 0;

    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    transition: $default-transition-settings;
  }

  &:hover {
    opacity: 0.7;
  }


  @media (max-width: 600px) {
    width: 445px;
    margin-right: 16px;

    h3 {
      font-weight: 600;
      font-size: 22px;
      line-height: 30px;
    }
  }

  @media (max-width: 480px) {
    width: 231px;
    margin-right: 11px;
    height: auto;

    h3 {
      font-weight: bold;
      font-size: 16px;
      line-height: 22px;
    }
  }
}

.recipes-slider__link {
  height: 100%;

  img {
    height: 187px;
    object-fit: cover;
    transition: $default-transition-settings;
  }

  @media (max-width: 600px) {
    img {
      height: 406px;
      width: 100%;
    }
  }

  @media (max-width: 480px) {
    img {
      height: 187px;
    }
  }
}

.recipes-slider__info {
  padding: 14px 24px 10px 15px;

  @media (max-width: 600px) {
    padding: 36px 132px 10px 4px;
  }

  @media (max-width: 480px) {
    padding: 12px 16px 10px 16px;
  }
}
