.page-sliders {
  position: relative;

  .swiper-container {
    width: calc(100% + 34px);
    margin-left: -18px;
    margin-top: -20px;
    padding-top: 20px;
    padding-left: 16px;
    padding-right: 16px;
  }

  .navigation-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    width: 47px;
    height: 47px;

    svg {
      width: 12px;
      height: 18px;
    }

    &--prev {
      left: -67px;
    }

    &--next {
      right: -67px;
      transform: translateY(-50%) rotate(-180deg);
    }
  }

  @media (max-width: 1024px) {
    .swiper-container {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  @media (max-width: 960px) {
    .navigation-arrow {
      display: none;
    }
  }

  @media (max-width: 600px) {
    .swiper-container {
      overflow: auto;
      width: calc(100% + 46px);
      margin-left: -46px;
    }
  }

  @media (max-width: 480px) {
    .swiper-container {
      margin-left: -51px;
      width: calc(100% + 51px);
    }
  }
}

.page-sliders__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: 960px) {
    overflow: auto;
    padding: 14px;
    margin-top: -14px;
    margin-left: -14px;

    li {
      margin-right: 16px;
      width: 212px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @media (max-width: 600px) {
    width: max-content;
    margin-left: -15px;
    margin-top: -10px;
    padding-top: 10px;
    padding-right: 31px;

    li {
      width: 186px;
      margin-right: 18px;

      &:first-child {
        margin-left: 31px;
      }

      &:last-child {
        width: 186px;
      }
    }
  }

  @media (max-width: 480px) {
    padding-right: 20px;
    padding-bottom: 0;

    li {
      &:first-child {
        margin-left: 36px;
      }
    }
  }
}

.page-sliders__item {
  width: initial;
  margin-bottom: 20px;
  height: auto;

  text-align: left;
  border: 1px solid $color-gainsboro;
  transition: $default-transition-settings;

  &:hover,
  &:focus {
    cursor: pointer;
    //filter: drop-shadow(0px 12px 30px rgba(0, 0, 0, 0.15));

    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15),
    0 6px 15px rgba(0, 0, 0, 0.15);
  }

  @media (max-width: 767px) and (min-width: 600px) {
    width: 188px;
  }
}

.page-sliders__block-top {
  margin-bottom: 20px;
}

.page-sliders__link {
  width: 100%;
  height: 100%;
  padding: 8px 7px 15px;

  min-height: 360px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: auto;

  img {
    margin-bottom: 7px;
    width: 100%;
    height: 155px;
    object-fit: contain;
  }

  h3 {
    margin: 0;
    padding-left: 7px;

    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    overflow-wrap: break-word;
  }

  button {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: auto;
    min-height: 30px;
    width: 90%;
    padding: 3px 0;

    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    vertical-align: middle;
    text-align: center;
    word-wrap: break-word;
    word-break: normal; /* для Opera и FireFox */
    /* stylelint-disable */
    word-break: break-word; /* для Chrome */
    /* stylelint-enable */

    background-color: transparent;
    border: 1px solid $color-silver;
    border-radius: 5px;
    transition: $default-transition-settings;

    &:hover,
    &:focus {
      color: $color-default-white;
      outline: none;
      cursor: pointer;
      background-color: $color-fallow;
    }
  }
}

.page-sliders__art {
  display: block;
  padding-left: 7px;

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  color: $color-silver;
}

.page-sliders__cost {
  display: block;
  margin-bottom: 20px;
  padding-left: 7px;

  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
}

.page-sliders__btn {
  position: absolute;
  top: 47%;
  width: 47px;
  height: 47px;

  border: none;
  background-color: transparent;
  font-size: 0;

  cursor: pointer;
  transition: $default-transition-settings;

  &--prev {
    left: -67px;

    @media (max-width: 1439px) and (min-width: 1024px) {
      left: -56px;
    }
  }

  &--next {
    right: -68px;

    -webkit-transform: rotate(45deg);
    transform: rotate(180deg);

    @media (max-width: 1439px) and (min-width: 1024px) {
      right: -57px;
    }
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    border: 1px solid $color-default-black;
    width: 47px;
    height: 47px;
    border-radius: 50px;
    transition: $default-transition-settings;
  }

  &::after {
    position: absolute;
    content: "";
    top: 35%;
    left: 40%;
    border: 1px solid $color-default-black;
    width: 15px;
    height: 15px;
    border-right: none;
    border-bottom: none;
    -webkit-transform: rotate(-45deg);
    transition: $default-transition-settings;
    transform: rotate(-45deg);
  }

  &:hover,
  &:focus {
    outline: none;
    cursor: pointer;

    &::before,
    &::after {
      border-color: $color-suva-grey;
    }
  }

  @media (max-width: 1023px) and (min-width: 280px) {
    display: none;
  }
}
