.pagination {
  max-width: 426px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include viewport--lg {
    width: 48%;
  }

  @media (max-width: 799px) {
    width: 64%;
  }

  @include viewport--sm {
    flex-wrap: wrap;
    width: 100%;
    max-width: 530px;
  }
}

.pagination__list {
  @include list-reset;

  width: 54.93%;
  max-width: 234px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include viewport--sm {
    margin: 0 0 20px 0;
    order: -1;
    max-width: 530px;
    width: 100%;
    justify-content: center;
  }
}

.pagination__item {
  width: 20%;
  max-width: 39px;
}

.pagination__link {
  display: block;
}

.pagination__next {
  svg {
    order: 1;
    transform: rotate(180deg);
  }
}

.pagination__prev {
  svg {
    transform: none;
  }
}
