.events-card {
  word-wrap: break-word;

  img {
    margin-bottom: 36px;
    max-width: 100%;
    height: 100%;
    max-height: 149px;
    object-fit: cover;

    @media (max-width: 1439px) and (min-width: 280px) {
      height: 150px;
    }
  }
}

.events-card__info-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 157px;
}

.events-card__info-title {
  margin: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
  text-transform: uppercase;

  color: $color-default-white;

  span {
    display: block;
    text-transform: initial;
    color: $color-default-white;
  }

  @media (max-width: 1024px) {
    overflow-wrap: break-word;
  }

  @media (max-width: 960px) {
    font-size: 16px;
    line-height: 22px;
  }

  @media (max-width: 600px) {
    font-size: 22px;
    line-height: 30px;
  }

  @media (max-width: 480px) {
    font-size: 16px;
    line-height: 22px;
  }
}

.events-card__info {
  width: 55%;
  display: flex;
  justify-content: space-between;
  margin: 0;

  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 30px;
  color: $color-default-white;

  @media (max-width: 1024px) {
    width: 65%;
  }

  @media (max-width: 600px) {
    width: 56%;
  }

  @media (max-width: 480px) {
    width: 63%;
  }
}

.events-card__date {
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 30px;

  color: $color-default-white;
}

.events-card__time {
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 30px;

  color: $color-default-white;
}
