.personal-data .h2 {
  margin: 0 0 35px 0;

  color: $color-gray-1;

  @include viewport--xs {
    margin-bottom: 11px;

    font-size: 19px;
    line-height: 26px;
  }
}

.personal-data__form .btn {
  width: 48.673%;
  margin-top: 37px;
  padding: 8px 64px;

  font-weight: 500;
  font-size: 16px;
  line-height: 22px;

  @include viewport--sm {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    margin: 22px auto 0;
    padding: 8px 122px;
  }

  @include viewport--xs {
    width: 100%;
    margin-top: 14px;
    padding: 12px 20px;
  }
}

.personal-data__form .form-col--2 {
  @include viewport--sm {
    flex-direction: column;
  }
}

.personal-data__form .form-field {
  @include viewport--sm {
    width: 100%;
  }
}
