.inner-menu {
  padding: 30px 0 0 0;

  width: 100%;

  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  overflow: visible;

  background-color: $color-black-2;

  h1 {
    margin: 0 0 20px 0;

    width: 100%;

    font-family: $font-family--avenir;
    font-weight: 400;
    font-size: 36px;
    line-height: 37.8px;
    text-align: center;

    color: $color-active-gold;
    text-transform: uppercase;
  }

  @include viewport--sm {
    h1 {
      margin: 0 0 20px -2px;
      text-align: left;
    }
  }

  @include viewport--xs {
    h1 {
      margin: 0 0 15px 0;
      font-size: 22px;
      line-height: 23.1px;
    }
  }

  &--centered {
    .inner-menu__list {
      display: flex;
      justify-content: center;
    }

    @media (max-width: 600px) {
      .inner-menu__list {
        justify-content: start;
      }
    }
  }
}

.inner-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: 829px) {
    justify-content: flex-start;
  }

  @include viewport--sm {
    display: block;
  }
}

