.inner-menu__list {
  @include list-reset;

  padding: 0 0 20px 0;
  width: 100%;
  max-width: 917px;

  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: auto;
  grid-column-gap: 2.51%;
  grid-row-gap: 20px;


  @include viewport--lg {
    width: 86.89%;
    grid-template-columns: repeat(5, minmax(138px, 1fr));
  }

  @media (max-width: 830px) {
    padding: 0 0 20px 0;
    width: 100%;
    grid-auto-columns: 138px;
    grid-auto-flow: column;
  }

  @include viewport--sm {
    width: 100%;
    padding: 0 0 30px 0;
  }

  @include viewport--xs {
    padding: 0 0 20px 0;
    grid-template-columns: repeat(5, minmax(120px, 1fr));
    grid-auto-columns: 120px;
  }
}

.inner-menu__item {
  border-radius: 5px;
  width: 165px;

  @include viewport--lg {
    width: 138px;
  }

  @include viewport--xs {
    width: 120px;
  }
}

.inner-menu__link {
  padding: 17px 8px 16px;

  display: block;

  width: 100%;

  font-family: $font-family--avenir;
  font-weight: 700;
  font-size: 16px;
  line-height: 21.86px;

  color: $color-gray-1;
  word-wrap: break-word;
  text-align: center;

  background-color: $color-default-white;
  border: 1px solid $color-white-smoke-2;
  border-radius: 5px;
  box-shadow: 0 1px 3px rgba($color-default-black, 0.1);
  transition: all linear 0.2s;

  &:hover,
  &:focus {
    background-color: $color-gold;
    color: $color-default-white;
    border: 1px solid $color-gold;
  }

  &:active {
    background-color: $color-active-gold;
    color: $color-default-white;
    border: 1px solid $color-active-gold;
  }

  &--active {
    background-color: $color-gold;
    color: $color-default-white;
    border: 1px solid $color-gold;
  }

  &--inactive {
    pointer-events: none;
    cursor: default;
    color: $color-default-white;
    background-color: $color-gainsboro-2;
    box-shadow: 0 1px 3px rgba($color-default-black, 0.1);
    border: 1px solid $color-gainsboro-2;
  }

  @include viewport--lg {
    padding: 9px 8px 8px;
  }

  @include viewport--xs {
    padding: 7px 8px 8px;

    font-size: 12px;
    line-height: 16.39px;
  }
}
