.button-round {
  position: relative;
  display: block;

  width: 47px;
  height: 47px;

  &::after {
    position: absolute;
    top: 0;
    left: 0;

    width: 47px;
    height: 47px;

    content: "";
    background-image: url("../img/content/icon-button-round.svg");
    background-repeat: no-repeat;
  }

  &:hover,
  &:focus {
    &::after {
      transform: rotate(90deg);

      background-image: url("../img/content/button-round-hover.svg");
      background-repeat: no-repeat;
    }
  }

  &--inactive {
    &::after {
      transform: rotate(90deg);

      background-image: url("../img/content/button-round-inactive.svg");
      background-repeat: no-repeat;
    }
  }
}
