.form-field {
  position: relative;

  display: flex;
  flex-direction: column;
  width: 100%;

  margin-bottom: 20px;

  @include viewport--xs {
    margin-bottom: 10px;
  }
}

.form-field label {
  position: absolute;
  top: 50%;
  left: 13px;

  display: inline-block;
  margin-top: -8px;

  font-size: 16px;
  line-height: 16px;
  color: $color-silver;
  font-family: $font-family--myseo;

  transition: all 0.2s ease;

  &.active {
    top: 12px;

    font-size: 11px;
    line-height: 12px;
  }
}

.form-field__textarea label {
  top: 21px;

  margin-top: 0;

  &.active {
    top: 8px;
  }
}

.form-wrapper input,
.form-wrapper textarea {
  width: 100%;
  padding: 15px 13px 12px;

  font-size: 16px;
  line-height: 16px;
  color: $color-matternhorn;
  font-family: $font-family--myseo;

  border: 1px solid $color-silver;
  border-radius: 10px;
  outline: none;
}

.form-wrapper textarea {
  padding-top: 20px;
  min-height: 154px;

  resize: none;
}

.form-col {
  display: flex;

  @include viewport--xs {
    flex-direction: column;
  }
}

.form-col--1 .form-field {
  width: 100%;
}

.form-col--2 {
  justify-content: space-between;
  flex-wrap: wrap;
}

.form-col--2 .form-field {
  width: 48.673%;

  @include viewport--lg {
    width: 48.8%;
  }

  @include viewport--xs {
    width: 100%;
  }
}

.form-col--3 {
  justify-content: space-between;
}

.form-col--3 .form-field {
  width: 31.121%;

  @include viewport--lg {
    width: 31.8%;
  }

  @include viewport--xs {
    width: 100%;
  }
}
