.about-intro {
  padding: 50px 0 177px;
  background-color: $color-default-white;

  @include viewport--lg {
    padding: 50px 0 47px;
  }

  @include viewport--sm {
    padding: 42px 0 0;
  }

  @include viewport--xs {
    padding: 30px 0 0;
  }
}

.about-intro__wrapper {
  position: relative;

  @include viewport--sm {
    display: block;
    padding: 0 30px 40px;
  }

  @include viewport--xs {
    padding: 0 20px 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}

.about-intro__picture {
  position: absolute;
  top: -30px;
  left: 635px;

  width: 710px;

  img {
    clip-path: polygon(168px 798px, 713px 611px, 713px 9px, 1px 110px);
  }

  @include viewport--lg {
    top: -45px;
    left: (472px + 15px);
    width: 472px;

    img {
      width: 472px;
      height: 494px;

      clip-path: polygon(105px 495px, 474px 365px, 472px -6px, 1px 66px);
    }
  }

  @media (max-width: 750px) {
    position: initial;
  }

  @include viewport--sm {
    width: 538px;

    img {
      width: 538px;
      height: 552px;

      clip-path: polygon(119px 554px, 540px 408px, 530px 1px, 0px 77px);
    }
  }

  @include viewport--xs {
    margin: 0;
    width: 320px;

    img {
      top: 680px;
      right: 0;

      width: 327px;
      height: 336px;

      clip-path: polygon(74px 338px, 316px 251px, 313px 0, 0 38px);
    }
  }
}

.about-intro__inner {
  max-width: 563px;
  width: 100%;

  @include viewport--lg {
    width: 52.44%;
  }

  @media (max-width: 750px) {
    width: 100%;
  }

  @include viewport--xs {
    margin: 0 0 5px 0;
  }
}

.about-intro__text {
  @include list-reset;

  margin: 68px 0 0 -5px;

  max-width: 395px;
  width: 100%;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include viewport--lg {
    margin: 80px 0 0 0;
    max-width: 410px;
  }

  @include viewport--sm {
    margin: 58px 0 0 0;
    width: 72%;
  }

  @include viewport--xs {
    margin: 50px 0 0 0;
    width: 100%;
  }
}

.about-intro__points {
  padding: 0 0 0 5px;

  .about-intro__descr {
    margin: 5px 0 0 6px;
  }

  @include viewport--lg {
    padding: 0;

    .about-intro__descr {
      margin: 0 0 0 8px;
    }
  }

  @include viewport--sm {

  }

  @include viewport--xs {
    margin: 0 0 35px -4px;
    width: 100%;
  }
}

.about-intro__space {
  max-width: 170px;
  width: 100%;

  .about-intro__num {
    margin: 0 0 0 -10px;
  }

  @include viewport--lg {
    width: 44%;

    .about-intro__num {
      margin: 0 0 0 -10px;
    }
  }

  @include viewport--sm {
    max-width: 185px;
  }

  @include viewport--xs {
    width: 100%;
  }
}

.about-intro__num {
  margin: 0;
  padding: 0;

  font-weight: 100;
  font-size: 70px;
  line-height: 75px;

  @include viewport--sm {
    margin: 0;
  }
}

.about-intro__descr {
  margin: 0;
  padding: 0;

  font-size: 14px;
  line-height: 19.12px;
  text-transform: uppercase;
}
