.radio-field label {
  display: inline-block;
  min-width: 142px;
  padding: 3px 17px;

  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
  color: $color-gray-1;
  text-align: center;
  vertical-align: middle;

  border: 1px solid $color-gainsboro;
  border-radius: 60px;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $color-default-white;

    background-color: $color-gold;
    border-color: $color-gold;
  }
}

.radio-field input:focus + label {
  color: $color-default-white;

  background-color: $color-gold;
  border-color: $color-gold;
}

.radio-field input:checked + label {
  color: $color-default-white;

  background-color: $color-active-gold;
  border-color: $color-active-gold;
}
