.schedule {
  margin: 0 0 27px;
  padding: 0;
  list-style: none;

  @include viewport--sm {
    margin: 0 0 30px;
  }
}

.schedule li {
  margin: 0;
  color: $color-default-black;
}

.schedule span {
  display: inline-block;
  min-width: 100px;

  @include viewport--sm {
    min-width: 80px;
  }
}

.schedule .uppercase {
  text-transform: uppercase;
}

.schedule .nowrap {
  white-space: nowrap;
}
