.header {
  z-index: 11;

  display: flex;
  min-height: 166px;

  background-color: $color-black;

  @include viewport--lg {
    min-height: 113px;
  }

  @media (max-width: 600px) {
    min-height: 72px;

    overflow: hidden;
  }

  @include viewport--sm {
    min-height: 72px;

    overflow: hidden;
  }


  &__logo {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.header .container {
  position: relative;

  display: flex;
  align-items: center;
  padding: 27px 30px 28px;

  @include viewport--lg {
    padding-left: 176px;
    padding-right: 36px;
  }

  @include viewport--md {
    padding: 26px 37px 23px 176px;
  }

  @media (max-width: 600px) {
    padding-top: 19px;
    padding-bottom: 19px;
  }
}

.header__logo img {
  @include viewport--lg {
    width: 177px;
    height: 113px;
  }

  @media (max-width: 600px) {
    height: 72px;
  }

  @include viewport--sm {
    height: 72px;
  }
}

.header__nav-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin: 0 0 0 25.6%;

  @include viewport--lg {
    margin-left: 0;
  }
}

.header__bottom {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: auto;

  @include viewport--lg {
    margin-top: 0;
    align-items: center;
  }
}

.header .form-search {
  max-width: 651px;
  width: 63.451%;
  margin-left: 12px;

  @include viewport--lg {
    max-width: 451px;
    width: 60.7%;
    margin: 0 auto 0 14%;
  }

  @include viewport--sm {
    display: none;
  }
}
