.dish-recipe {
  padding-top: 36px;
  padding-bottom: 63px;

  .breadcrumbs {
    margin-bottom: 56px;
  }

  .breadcrumbs__link {
    color: black;
  }

  .stepper {
    opacity: 1;
  }

  .portions {
    padding-top: 7px;
    margin-right: 15px;
  }

  .columns-list {
    margin-bottom: 39px;
  }

  .counter-list {
    width: 49.1%;
    margin-left: auto;
  }

  h1 {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;

    color: $color-gray-1;
  }

  @media (max-width: 1024px) {
    padding-top: 26px;
    padding-bottom: 51px;

    .breadcrumbs {
      margin-bottom: 46px;
    }

    .container {
      padding: 0 30px;
    }

    .dish-recipe__img-wrapper {
      width: 40.2%;
      height: 327px;
      margin-left: 9%;
    }

    .dish-recipe__recipe-wrapper {
      padding-top: 10px;
    }

    .columns-list {
      padding: 29px 15px 25px 30px;
      margin-bottom: 29px;

      ul {
        columns: 1;
      }
    }

    .counter-list {
      width: 100%;

      li {
        margin-bottom: 41px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  @media (max-width: 960px) {
    padding-bottom: 39px;

    .breadcrumbs {
      margin-bottom: 44px;
    }

    .dish-recipe__img-wrapper {
      margin-left: 0;
      width: 48.9%;
      height: 370px;
    }

    .dish-recipe__recipe-wrapper {
      padding-top: 1px;
    }

    .columns-list {
      margin-bottom: 41px;
      padding: 29px 15px 15px 30px;
    }
  }

  @media (max-width: 800px) {
    padding-top: 39px;
    padding-bottom: 48px;

    .breadcrumbs {
      margin-bottom: 33px;

      ul {
        justify-content: start;
      }
    }

    .dish-recipe__img-wrapper {
      margin-bottom: 40px;
      width: 100%;
      height: 454px;
    }

    .dish-recipe__recipe-wrapper {
      width: 100%;
    }

    .dish-recipe__wrapper {
      flex-direction: column;
    }

    .columns-list {
      margin-bottom: 51px;
      padding: 29px 15px 36px 30px;
    }
  }

  @media (max-width: 480px) {
    padding-top: 24px;
    padding-bottom: 29px;

    .breadcrumbs {
      padding-top: 15px;
      margin-bottom: 29px;
      border-top: 1px solid rgba($color: $color-active-gold, $alpha: 0.4);
    }

    h1 {
      margin-bottom: 6px;
      font-size: 19px;
      line-height: 26px;
    }

    .stepper {
      width: 140px;
      height: 41px;
      margin-top: auto;

      span {
        position: absolute;
        top: 7px;
        right: 38px;
        font-size: 18px;
        font-weight: 600;
        line-height: 25px;
      }
    }

    .stepper__btn--plus {
      font-size: 36px;
      line-height: 36px;
      font-weight: 400;
    }

    .stepper__btn--minus {
      font-size: 25px;
      font-weight: 500;
    }

    .dish-recipe__order {
      margin-bottom: 31px;
    }

    .columns-list {
      padding: 28px 15px 16px 9px;
      margin-bottom: 31px;
    }

    .counter-list {
      li {
        margin-bottom: 44px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .container {
      padding: 0 20px;
    }

    .dish-recipe__img-wrapper {
      height: 234px;
      margin-bottom: 31px;
    }

    .portions {
      flex-direction: column;
      padding-bottom: 10px;

      & > div {
        margin-bottom: 25px;
        margin-right: 1px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

.dish-recipe__back {
  align-items: center;
  margin-bottom: 34px;
  width: max-content;
  display: none;

  svg {
    transform: rotate(-180deg);
    margin-right: 13px;
  }

  p {
    margin: 0;
  }

  @media (max-width: 800px) {
    display: flex;
  }

  @media (max-width: 480px) {
    margin-bottom: 19px;

    svg {
      margin: 0;
    }

    p {
      display: none;
    }
  }
}

.dish-recipe__order {
  display: flex;
  margin-bottom: 50px;
}

.dish-recipe__wrapper {
  display: flex;
  justify-content: space-between;
}

.dish-recipe__img-wrapper {
  width: 443px;
  height: 373px;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.dish-recipe__recipe-wrapper {
  width: 49.1%;
}
