.round-icon {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;

  border: 1px solid $color-default-white;
  border-radius: 100px;
  outline: none;
  fill: $color-default-white;

  &:hover,
  &:focus {
    border-color: $color-gold;
    fill: $color-gold;
  }

  &:active {
    background-color: $color-gold;
    fill: $color-default-white;
  }
}

.round-icon span {
  position: absolute;
  top: -10px;
  right: -6px;

  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 20px;
  padding: 2px 3px;

  font-family: $font-family--myseo;
  font-size: 11px;
  font-weight: 700;
  line-height: 14px;
  color: $color-default-white;

  background-color: $color-gold;
  border: 1px solid $color-default-white;
  border-radius: 100px;
  overflow: hidden;
}
