.category-list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-top: -4px;

  max-height: 69.2px;
  height: 100%;
  overflow: auto;
  width: calc(100% - 84px);

  @media (max-width: 1024px) {
    padding-right: 80px;
  }

  @media (max-width: 960px) {
    max-height: 34.6px;
    padding-right: 0;
    flex-wrap: nowrap;

    li {
      a {
        width: max-content;
      }
    }
  }

  @media (max-width: 600px) {
    max-height: 34.6px;
    width: calc(100% - 84px);
  }

  @media (max-width: 480px) {
    margin-left: -13px;
    width: calc(100% - 30px);
  }

  li {
    margin-right: 16px;
    margin-top: 4px;

    transition: background-color $default-transition-settings;
    border-radius: 60px;

    a {
      display: block;
      padding: 5px 23px 4px;

      font-weight: 500;
      font-size: 16px;
      line-height: 22px;

      color: $color-gray-1;
      transition: color $default-transition-settings;


      @media (max-width: 480px) {
        font-size: 14px;
        line-height: 19px;

        padding: 5px 13px 4px;
      }
    }

    &.is-active {
      background-color: $color-active-gold;
      border-radius: 60px;

      a {
        color: white;
      }
    }

    &:hover {
      background-color: $color-active-gold;

      a {
        color: white;
      }
    }

    @media (max-width: 480px) {
      margin-right: 2px;
    }
  }
}
