.items-selected {
  display: flex;
  align-items: center;
  justify-content: space-between;

  p {
    margin: 0;

    font-weight: 700;
    font-size: 12px;
    line-height: 16px;

    color: $color-suva-grey;
  }

  @media (max-width: 799px) {
    flex-direction: column;

    p {
      order: 1;
    }
  }
}

.items-selected__form {
  position: relative;

  &::after {
    position: absolute;
    top: 13px;
    right: 8px;

    width: 15px;
    height: 10px;

    content: "";
    background-image: url("../img/content/dropdown.svg");
    background-repeat: no-repeat;
  }

  select {
    padding: 10px 12px 8px;
    width: 60px;
    border: none;
    border-radius: 60px;
    background-color: $color-whisper;

    font-family: $font-family--avenir;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: $color-zambezi;

    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
  }
}

.items-selected__select {
  width: 60px;
  position: relative;

  &:hover {
    opacity: 0.7;
  }
}

.items-selected__header {
  width: 60px;
  padding: 10px 12px 8px;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 60px;
  background-color: $color-whisper;
  cursor: pointer;
}

.items-selected__icon {
  position: relative;
  width: 15px;
  height: 10px;

  &::after {
    position: absolute;
    top: 0;
    left: 0;
    width: 15px;
    height: 10px;

    content: "";

    background-image: url("../img/content/dropdown.svg");
    background-repeat: no-repeat;
  }
}

.items-selected__current {
  width: 45px;

  font-family: $font-family--avenir;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: $color-zambezi;
}

.items-selected__body {
  display: none;
  padding: 0 0 10px 0;
  position: absolute;
  right: 0;
  left: 0;
  bottom: calc(100% - 11px);

  border-top-left-radius: 17px;
  border-top-right-radius: 17px;
  background-color: $color-whisper;
}

.items-selected__item {
  padding: 10px 12px 8px;
  cursor: pointer;

  font-family: $font-family--avenir;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: $color-zambezi;

  &:first-of-type {
    border-top-left-radius: 17px;
    border-top-right-radius: 17px;
  }

  &:hover,
  &:focus {
    background-color: $color-suva-grey;
  }
}

.is-active {
  .items-selected__body {
    display: block;
  }
}
