.product-cards {
  max-width: 1440px;
  padding: 36px 31px 0;

  @include viewport--lg {
    padding-top: 25px;
  }

  @include viewport--sm {
    padding-top: 40px;
  }

  @include viewport--xs {
    padding-top: 24px;
  }
}

.product-cards__back-block {
  display: none;

  @include viewport--sm {
    display: block;
    margin-bottom: 35px;
  }
}

.product-cards__back-block a {
  display: flex;
}

.product-cards__back-block svg {
  order: -1;
  transform: rotate(180deg);
  margin-left: 0;
  margin-right: 13px;
}

.product-cards__back-block span {
  @include viewport--xs {
    display: none;
  }
}

.product-cards__similar {
  padding-top: 42px;
  padding-bottom: 63px;

  @include viewport--lg {
    padding-top: 22px;
    padding-bottom: 31px;
  }

  @include viewport--sm {
    padding-top: 12px;
    padding-bottom: 37px;
  }

  @include viewport--xs {
    padding-bottom: 30px;
  }
}

.product-cards__similar .page-sliders__item {
  margin-bottom: 0;
}

.product-cards__similar-title {
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 38px;

  @include viewport--lg {
    margin-right: 40px;
    margin-bottom: 22px;
  }

  @include viewport--sm {
    margin-right: 0;
  }

  @include viewport--xs {
    text-align: left;
  }
}

.product-cards__related {
  padding-top: 0;
  padding-bottom: 50px;
  background-color: $color-black-2;

  @include viewport--lg {
    padding-top: 38px;
  }

  @include viewport--sm {
    padding-top: 36px;
  }

  @include viewport--xs {
    padding-top: 17px;
  }
}

.product-cards__related-title {
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  color: $color-default-white;
}
