.market-area {
  padding-top: 97px;
  padding-bottom: 80px;
  position: relative;
  background-color: $color-melanzane;

  @media (max-width: 1024px) {
    padding-top: 105px;
    padding-bottom: 80px;
  }

  @media (max-width: 960px) {
    padding-top: 39px;
    padding-bottom: 51px;
  }

  @media (max-width: 600px) {
    padding-top: 39px;
    padding-bottom: 47px;
  }

  @media (max-width: 480px) {
    padding-top: 29px;
    padding-bottom: 37px;
  }
}

.market-area__wrapper {
  display: flex;
  min-height: 325px;
  justify-content: space-between;
  margin-bottom: 65px;

  @media (max-width: 1024px) {
    margin-bottom: 39px;
    padding: 0 30px;
    min-height: 228px;
  }

  @media (max-width: 600px) {
    flex-direction: column;
    margin-bottom: 33px;
  }

  @media (max-width: 480px) {
    padding: 0 20px;
    margin-bottom: 29px;
  }
}

.market-area__description {
  width: 39.8%;

  @media (max-width: 1024px) {
    width: 48%;
  }

  @media (max-width: 960px) {
    width: 51%;
  }

  @media (max-width: 600px) {
    width: 85%;
    margin-bottom: 34px;
  }

  @media (max-width: 480px) {
    width: 100%;
    margin-bottom: 39px;
  }
}

.market-area__title {
  //width: 77%;
  margin: 0;
  padding-bottom: 20px;

  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 38px;
  //text-align: center;
  text-transform: uppercase;
  color: $color-fallow;

  @media (max-width: 600px) {
    padding-bottom: 24px;
  }

  @media (max-width: 599px) and (min-width: 280px) {
    font-size: 22px;
    line-height: 23px;
  }
}

.market-area__info {
  //width: 79%;
  margin: 0;
  padding-bottom: 20px;

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $color-default-white;
}

.market-area__link-more {
  justify-content: flex-start;
  width: fit-content;

  color: $color-default-white;

  svg {
    fill: $color-default-white;
  }
}

.market-area__img {
  position: absolute;
  right: 0;
  width: 49.5%;
  display: flex;
  justify-content: space-between;
  max-height: 322px;
  overflow: hidden;

  img {
    width: 447px;
    height: 325px;

    object-fit: cover;
  }

  @media (max-width: 1024px) {
    width: 49.2%;

    img {
      width: 313px;
      height: 228px;
    }
  }

  @media (max-width: 960px) {
    overflow: auto;
    width: 42.2%;

    img {
      margin-right: 14px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @media (max-width: 600px) {
    position: static;
    width: calc(100% + 30px);
  }

  @media (max-width: 480px) {
    img {
      margin-right: 10px;
      width: 232px;
      height: 169px;
    }
  }
}
