.submenu {
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 0 0 0 20px;

  list-style: none;

  &::after {
    content: "";

    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 1px;

    background-color: $color-active-gold;
    opacity: 0.4;
  }

  &--opened {
    display: flex;
  }

  a {
    position: relative;

    display: flex;
    align-items: center;
    padding: 8px 19px 12px;

    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: $color-default-white;

    &::after {
      content: "";

      position: absolute;
      bottom: 0;
      left: 0;

      width: 100%;
      height: 1px;

      background-color: $color-active-gold;
      opacity: 0.2;
    }
  }

  li:last-child a::after {
    display: none;
  }
}

.submenu-toggle span {
  max-width: 240px;
}

.submenu-toggle svg {
  stroke: $color-default-white;
  transition: all 0.3s ease;
}

.submenu-toggle--active svg {
  transform: rotateX(-180deg);
}
