.user-profile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;

  border: 1px solid $color-default-white;
  border-radius: 100px;
  outline: none;
  fill: $color-transparent;
  stroke: $color-default-white;

  &:hover,
  &:focus {
    border-color: $color-gold;
  }

  &:active {
    background-color: $color-gold;
  }

  &:hover svg,
  &:focus svg {
    stroke: $color-gold;
  }

  &:active svg {
    stroke: $color-default-white;
  }
}
