.arendators {
  padding: 101px 0 40px;
  background-color: $color-default-white;

  @include viewport--lg {
    padding: 50px 0 45px;
  }

  @include viewport--sm {
    padding: 42px 0 50px;
  }

  @include viewport--xs {
    padding: 30px 0 23px;
  }
}

.arendators__wrapper {
  position: relative;

  @include viewport--sm {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.arendators__info {
  width: 100%;
  max-width: 563px;

  @include viewport--lg {
    width: 52.44%;
  }

  @media (max-width: 750px) {
    margin: 0 0 20px 0;
    width: 100%;
  }

  @include viewport--xs {
    margin: 0 0 30px 0;
  }
}

.arendators__picture {
  position: absolute;
  top: -48px;
  left: 605px;

  width: 730px;

  img {
    object-fit: cover;
    clip-path: polygon(1px 397px, 730px 511px, 672px 1px, 106px 72px);
  }

  @media (max-width: 1200px) {
    left: 500px;

    img {
      transform: scale(0.85);
    }
  }

  @media (max-width: 1024px) {
    left: 405px;

    img {
      transform: scale(0.73);
    }
  }

  @include viewport--lg {
    top: 0;
    left: (468px + 20px);
    width: 468px;

    img {
      width: 468px;
      height: 332px;

      clip-path: polygon(0px 247px, 467px 331px, 413px 3px, 65px 43px);
      transform: scale(1);
    }
  }

  @media (max-width: 750px) {
    position: initial;
  }

  @include viewport--sm {
    width: 538px;

    img {
      width: 538px;
      height: 382px;

      clip-path: polygon(1px 282px, 536px 384px, 479px 5px, 75px 49px);
    }
  }

  @include viewport--xs {
    width: 320px;

    img {
      width: 320px;
      height: 255px;

      clip-path: polygon(2px 189px, 326px 244px, 317px 3px, 48px 32px);

    }
  }
}
