.delivery-address__list {
  margin: 0 0 48px 0;
  padding: 0;

  list-style: none;
}

.delivery-address__item {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 14px;
  padding: 21px 85px 20px 20px;

  background-color: rgba($color-gainsboro-3, 0.2);

  &:last-child {
    margin-bottom: 0;
  }

  @include viewport--sm {
    flex-direction: column;
    align-items: flex-start;
    padding-right: 55px;
  }
}

.delivery-address__item b {
  margin-right: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: $color-default-black;

  @include viewport--sm {
    margin: 0 0 10px 0;
  }
}

.delivery-address__edit-btn {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: $color-eclipse;

  &:hover,
  &:focus {
    color: $color-gold;
  }

  &:active {
    color: $color-active-gold;
  }
}

.delivery-address__remove-btn {
  position: absolute;
  right: 30px;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;

  outline: none;

  &:hover svg,
  &:focus svg {
    fill: $color-gold;
  }

  &:active svg {
    fill: $color-active-gold;
  }

  @include viewport--sm {
    top: 13px;
    right: 10px;
  }
}

.delivery-address__add-btn {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 364px;
  margin: 0 auto;
}
