.main-events {
  padding: 59px 0 31px;
  padding-top: 62px;
  padding-bottom: 27px;
  background-color: $color-temptress;

  @media (max-width: 1024px) {
    padding-top: 58px;
    padding-bottom: 32px;
  }

  @media (max-width: 960px) {
    padding-top: 40px;
    padding-bottom: 29px;
  }

  @media (max-width: 600px) {
    padding-top: 50px;
    padding-bottom: 32px;
  }

  @media (max-width: 480px) {
    padding-top: 30px;
    padding-bottom: 6px;
  }
}

.main-events__container {
  @media (max-width: 1024px) {
    padding: 0 30px;
  }

  @media (max-width: 480px) {
    padding: 0 20px;
  }
}

.main-events__title {
  margin: 0;
  padding-bottom: 50px;

  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;
  color: $color-default-white;

  @media (max-width: 1024px) {
    padding-bottom: 54px;
  }

  @media (max-width: 1023px) and (min-width: 960px) {
    padding-bottom: 30px;
  }

  @media (max-width: 959px) and (min-width: 600px) {
    text-align: left;
    padding-bottom: 64px;
  }

  @media (max-width: 599px) and (min-width: 280px) {
    padding-bottom: 30px;
    text-align: left;
    font-size: 22px;
    line-height: 23px;
  }
}

.main-events__list {
  display: flex;
  flex-wrap: nowrap;
  //justify-content: space-between;

  list-style: none;
  margin: 0;
  padding: 0;

  li:last-child {
    margin-right: 0;
  }

  @media (max-width: 960px) {
    overflow: auto;
  }

  @media (max-width: 480px) {
    width: max-content;
    padding: 0 20px;
  }
}

.main-events-item {
  width: 23.3%;
  margin-bottom: 2.26%;
  margin-right: 27px;

  transition: opacity $default-transition-settings;

  &:hover {
    opacity: 0.7;
  }

  @media (max-width: 960px) {
    margin-right: 16px;
    width: 213px;
  }

  @media (max-width: 600px) {
    width: 261px;
    margin-right: 19px;
  }

  @media (max-width: 480px) {
    width: 232px;
    margin-right: 10px;
  }
}
