.recipes {
  padding-top: 40px;
  padding-bottom: 58px;

  .filter-list {
    @include list-reset;
    margin-bottom: 20px;
  }

  @media (max-width: 1024px) {
    padding-top: 31px;
    padding-bottom: 50px;

    .container {
      padding: 0 30px;
    }
  }

  @media (max-width: 800px) {
    padding-top: 30px;

    .filter-list {
      margin-bottom: 40px;
    }
  }

  @media (max-width: 480px) {
    padding-top: 19px;
    padding-bottom: 30px;

    .filter-list {
      margin-bottom: 10px;
    }

    .container {
      padding: 0 20px;
    }
  }
}

.recipes__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-top: -16px;

  @media (max-width: 960px) {
    margin-top: -20px;
  }
}
