.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include viewport--sm {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 580px;
    min-height: 284px;
    padding: 26px 60px;

    background-color: $color-default-white;

    @include viewport--sm {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 20px;
    right: 20px;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;

    border: 1px solid $color-gray-1;
    border-radius: 100px;
    outline: none;

    svg {
      stroke: $color-gray-1;
    }

    &:hover,
    &:focus {
      border-color: $color-gold;
    }

    &:hover svg,
    &:focus svg {
      stroke: $color-gold;
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include viewport--sm {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}

.modal__header {
  position: relative;

  display: flex;
  align-items: center;
  margin-bottom: 51px;

  h2 {
    margin: 0;
    padding: 0;

    font-size: 22px;
    font-weight: 600;
    line-height: 30px;
    color: $color-gray-1;
  }

  &--with-arrow {
    justify-content: center;

    h2 {
      text-align: center;
      max-width: 300px;
    }
  }
}

.modal__body form {
  margin-bottom: 29px;
}

.modal input {
  width: 100%;
  padding: 7px 0;

  font-size: 16px;
  color: $color-default-black;
  line-height: 23px;

  border: 0;
  border-bottom: 1px solid $color-whisper;
  outline: none;

  &::placeholder {
    color: $color-default-black;

    opacity: 0.3;
  }

  &:hover,
  &:focus {
    border-color: $color-gold;
  }
}

.modal form button[type="submit"] {
  margin-top: 29px;
}

.modal .modal__btn-prev {
  position: absolute;
  top: 6px;
  left: -2px;

  svg {
    transform: rotate(180deg);
  }

  &:hover,
  &:focus {
    fill: $color-gold;
  }
}
