.payment h2 {
  margin: 0 0 23px 2px;
  color: $color-gray-1;

  @include viewport--xs {
    margin: 0 0 15px 0;

    font-size: 19px;
    line-height: 26px;
  }
}

.payment__radio-group {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  list-style: none;
}

.payment__radio-group .radio-field {
  margin: 0 10px 15px 0;

  &:last-child {
    margin-right: 0;
  }

  @include viewport--xs {
    margin: 0 5px 12px 0;
  }
}

.payment__radio-group label {
  @include viewport--sm {
    padding: 12px 17px;
  }

  @include viewport--xs {
    min-width: 121px;
    padding: 4px 13px;

    font-size: 14px;
    line-height: 19px;
  }
}
