.inner-slider-wrap {
  overflow: hidden;
  padding-bottom: 0;
  margin-bottom: 0;

  & + .about-features {
    margin-top: 0;
    padding-top: 0;
  }
}

.inner-slider {
  margin: 0 0 17px 0;
  background-color: $color-black;

  max-width: 912px;
  width: 100%;


  @include viewport--lg {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @include viewport--sm {
    margin: 0;
    max-width: 546px;
  }

  @include viewport--xs {
    margin: 0 -6px 30px 0;
    width: 100%;
    max-width: 232px;
  }
}

.inner-slider__wrapper {
  overflow: visible!important;

  @include viewport--lg {
    padding: 0 0 30px 0;
  }

  @include viewport--sm {
    padding: 0 0 100px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @include viewport--xs {
    padding: 0;
  }
}

.inner-slider__pictures {
  width: 100%;
  background-color: $color-black;
}

.inner-slider__item {
  margin: 0;

  width: 100%;
  transition: height 0.3s ease;

  &::after {
    content: "";
    background-color: $color-black;
    position: absolute;
    bottom: -1px;
    left: -1px;
    right: -1px;
    height: 40%;
    z-index: 2;
    transform: translateY(100%);
    transition: transform 0.3s ease;
  }

  &:not(.swiper-slide-active) {
    &::after {
      transform: translateY(0);
    }
  }

  img {
    width: 100%;
    object-fit: cover;
  }

  @include viewport--lg {
    img {
      height: 483px;
    }

    &::after {
      height: 101%;
    }
  }

  @include viewport--sm {
    img {
      height: 293px;
    }
  }

  @include viewport--xs {
    img {
      height: 169px;
    }

    &::after {
      display: none;
    }
  }
}

.inner-slider__btns {
  margin: 52px 0 0 0;
  padding: 0;

  width: 118px;
  height: 47px;

  display: flex;
  justify-content: space-between;

  @include viewport--lg {
    margin: 3px 0 0 0;
  }

  @include viewport--sm {
    margin: 47px 0 0 0;
  }

  @include viewport--xs {
    display: none;
  }
}

.inner-slider__btn {
  position: static;
  top: 0;
  width: 47px;
  height: 47px;

  display: block;

  &::before {
    position: absolute;
    top: 0;
    left: 0;

    width: 47px;
    height: 47px;

    content: "";
  }

  &--prev {
    position: relative;

    &::before {
      left: -8px;
      background-image: url("../img/content/slide-prev.svg");
      background-repeat: no-repeat;
    }

    &:hover,
    &:focus {
      &::before {
        background-image: url("../img/content/button-round-hover.svg");
        background-repeat: no-repeat;
      }
    }
  }

  &--next {
    position: relative;

    &::before {
      left: 8px;
      background-image: url("../img/content/slide-next.svg");
      background-repeat: no-repeat;
    }

    &:hover,
    &:focus {
      &::before {
        background-image: url("../img/content/button-round-hover.svg");
        background-repeat: no-repeat;
        transform: rotate(180deg);
      }
    }
  }

  &--inactive-prev {
    pointer-events: none;
    cursor: default;

    &::before {
      background-image: url("../img/content/button-round-inactive.svg");
      background-repeat: no-repeat;
    }
  }

  &--inactive-next {
    pointer-events: none;
    cursor: default;

    &::before {
      background-image: url("../img/content/button-round-inactive.svg");
      background-repeat: no-repeat;
      transform: rotate(180deg);
    }
  }

  &::after {
    display: none;
  }
}
