.button-cart {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-width: 100px;
  min-height: 30px;
  padding: 3px 0;

  font-family: "AvenirNext", "Verdana", serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  vertical-align: middle;
  text-align: center;

  word-wrap: break-word;

  background-color: transparent;
  border: 1px solid $color-silver;
  border-radius: 5px;

  &:hover,
  &:focus {
    color: $color-default-white;
    outline: none;
    cursor: pointer;
    background-color: $color-fallow;

    transition: $default-transition-settings;
  }

  &:not(:hover) {
    transition: $default-transition-settings;
  }

  &--color {
    color: $color-default-white;
  }

  &--color-gray {
    color: $color-gray-1;
  }

  &--size {
    @media (max-width: 767px) and (min-width: 280px) {
      min-width: 151px;
      max-height: 25px;
    }
  }
}
