.modal--product .modal__wrapper {
  padding: 36px 0;

  @include viewport--lg {
    padding: 36px 50px;
  }

  @include viewport--xs {
    padding: 36px 0;
  }
}

.modal--product .modal__content {
  width: 100%;
  max-width: 1440px;
  padding: 40px 31px;
  background-color: $color-default-white;

  @include viewport--sm {
    padding: 84px 30px 40px;
  }

  @include viewport--xs {
    padding: 84px 20px 40px;
  }
}

.modal--product .product-cards-product {
  border-bottom: none;

  @include viewport--sm {
    padding-bottom: 0;
  }
}

.modal--product .product-cards-product__gallery {
  margin-bottom: 33px;
}

.modal--product .product-cards-product__info {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.modal--product .product-cards-product__info h1 {
  margin-bottom: 16px;
}

.modal--product .product-cards-product__text {
  margin-bottom: 10px;
}

.modal--product .product-cards-product__link-all {
  @include viewport--sm {
    display: none;
  }
}
