.bonus__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.bonus__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 25px;
  padding: 13px 17px 12px 24px;

  background-color: rgba($color-gainsboro-3, 0.2);

  @include viewport--xs {
    flex-direction: column;
    align-items: flex-start;
  }
}

.bonus__item b {
  width: 60%;
  margin-right: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 21px;
  color: $color-default-black;

  @media (max-width: 770px) {
    width: 50%;
  }

  @media (max-width: 520px) {
    width: 45%;
  }

  @include viewport--xs {
    width: 100%;
    margin: 0 0 20px 0;
  }
}

.bonus__item .btn {
  padding: 7px 76px;

  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: $color-gray-1;

  &:hover,
  &:focus {
    color: $color-default-white;
  }

  @include viewport--sm {
    padding: 7px 50px;
  }

  @include viewport--xs {
    width: 100%;
  }
}
