.restaurants-dish {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.restaurants-dish__image {
  width: 100%;
}

.restaurants-dish img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.restaurants-dish__description {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 12px 14px 11px;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  box-sizing: border-box;
  border: 1px solid $color-gainsboro;
  border-top: none;

  @include viewport--lg {
    padding: 8px 12px 20px;
  }
}

.restaurants-dish__description h4 {
  margin: 0 0 9px;
  color: $color-gray-1;

  @include viewport--lg {
    margin: 0 0 16px;
  }

  @include viewport--sav {
    margin: 0 0 39px;
  }
}

.restaurants-dish__text span {
  padding-right: 10px;

  @include viewport--lg {
    margin: 0 0 10px;
  }

  @include viewport--sav {
    margin: 0 0 29px;
  }

  @include viewport--xs {
    margin: 0 0 48px;
  }
}

.restaurants-dish__text span:first-of-type {
  position: relative;
  padding-right: 21px;

  &::after {
    position: absolute;
    content: "•";
    top: 0.5px;
    right: 8px;
  }
}

.restaurants-dish__buy {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @include viewport--lg {
    flex-direction: column;
    align-items: flex-start;
  }
}

.restaurants-dish__buy p {
  margin: 3px 5px 0 0;

  @include viewport--lg {
    margin-top: 14px;
    margin-bottom: 13px;
  }
}

.restaurants-dish__buy .button-cart {
  @include viewport--lg {
    width: 97%;
  }
}
