.main-recipes {
  position: relative;
  padding-top: 60px;
  padding-bottom: 60px;
  max-width: 1247px;

  @media (max-width: 1024px) {
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (max-width: 960px) {
    padding-top: 39px;
    padding-bottom: 96px;
  }

  @media (max-width: 480px) {
    padding-bottom: 43px;
    padding-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.main-recipes__wrapper {
  @media (max-width: 960px) {
    width: 100%;
    margin-left: 0;
  }

  @media (max-width: 600px) {
    width: calc(100% + 60px);
    margin-left: -30px;
    overflow: auto;
  }

  @media (max-width: 480px) {
    width: calc(100% + 40px);
    margin-left: -20px;
  }
}

.main-recipes__title {
  margin: 0 0 50px;

  font-weight: normal;
  font-size: 36px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;

  @media (max-width: 960px) {
    margin-bottom: 30px;
  }

  @media (max-width: 600px) {
    text-align: left;
  }

  @media (max-width: 480px) {
    text-align: left;
    font-size: 22px;
    line-height: 105%;
  }

  @media (max-width: 480px) {
    margin-bottom: 18px;
  }
}

.main-recipes__info-left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 154px;
}

.main-recipes__block-top {
  h3 {
    margin: 0 0 10px;

    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
  }

  p {
    margin: 0;
    width: 68%;

    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
  }
}

.main-recipes__block-button {
  display: flex;
  justify-content: space-between;
  width: 46%;

  span {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
  }

  svg {
    margin-right: 7px;
  }
}

.main-recipes__block-time {
  display: flex;
}

.main-recipes__block-feed {
  display: flex;
}

.main-recipes__container {
  width: 58.79%;
}
