.restaurants-cards {
  width: 100%;
  margin-bottom: 64px;

  @include viewport--lg {
    margin-bottom: 30px;
  }

  @include viewport--sav {
    margin-bottom: 20px;
  }

  @include viewport--xs {
    margin-bottom: 23px;
  }
}

.restaurants-cards__top {
  background-color: $color-black-2;
}

.restaurants-cards__navigation {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  padding-top: 36px;

  @include viewport--lg {
    padding-top: 26px;
  }

  @include viewport--sm {
    padding-top: 98px;
  }

  @include viewport--xs {
    padding-top: 59px;
  }
}

.restaurants-cards__back-block {
  position: absolute;
  top: 40px;
  left: 50px;

  @include viewport--lg {
    top: 30px;
    left: 30px;
  }

  @include viewport--sm {
    top: 41px;
  }

  @include viewport--xs {
    top: 24px;
    left: 20px;
  }
}

.restaurants-cards__back-block a {
  display: flex;
}

.restaurants-cards__back-block svg {
  order: -1;
  transform: rotate(180deg);
  margin-left: 0;
  margin-right: 13px;
}

.restaurants-cards__back-block span {
  @include viewport--xs {
    display: none;
  }
}

.restaurants-cards__navigation .breadcrumbs {
  @include viewport--xs {
    border-top: 1px solid rgba(188, 153, 107, 0.4);
    padding-top: 16px;
  }
}

.restaurants-cards__navigation .breadcrumbs__link {
  opacity: 1;
}

.restaurants-cards .card-intro__description p {
  margin-bottom: 0;
}

.restaurants-cards__dishes {
  padding-top: 15px;

  @include viewport--lg {
    padding-top: 11px;
  }

  @include viewport--sav {
    padding-top: 2px;
  }

  @include viewport--xs {
    padding-top: 8px;
  }
}

.restaurants-cards__dishes h2 {
  margin-bottom: 32px;
  text-transform: uppercase;

  @include viewport--xs {
    margin-bottom: 12px;
  }
}

.restaurants-cards__dishes-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  width: calc(100% + 16px);

  @include viewport--xs {
    width: 100%;
  }
}

.restaurants-cards__dishes-item {
  width: calc(25% - 16px);
  min-height: 335px;
  margin-right: 16px;
  margin-bottom: 16px;

  @include viewport--lg {
    min-height: 450px;
    margin-bottom: 10px;
  }

  @include viewport--sav {
    width: calc(50% - 16px);
    margin-bottom: 20px;
  }

  @include viewport--xs {
    width: 100%;
    min-height: 460px;
    margin-right: 0;
    margin-bottom: 9px;
  }
}

.restaurants-cards__dishes--notitle {
  padding-top: 0;
}

.restaurants-cards__dishes-item--big {
  width: calc(100% - 16px * 2 - (25% - 16px));

  @include viewport--sav {
    width: 100%;
    min-height: 300px;
  }

  @include viewport--sm {
    min-height: 350px;
  }
}

.restaurants-cards__dishes--full li {
  &:nth-child(n + 5) {
    @include viewport--sav {
      display: none;
    }
  }

  &:nth-child(n + 3) {
    @include viewport--xs {
      display: none;
    }
  }
}

.restaurants-cards__dishes--half {
  @include viewport--sav {
    display: none;
  }
}

.restaurants-cards__dishes--half li {
  &:nth-child(n + 5) {
    @include viewport--lg {
      display: none;
    }
  }
}

.restaurants-cards__dishes--mini li {
  &:nth-child(n + 3) {
    @include viewport--lg {
      display: none;
    }

    @include viewport--sav {
      display: block;
    }
  }

  &:nth-child(n + 4) {
    @include viewport--sav {
      display: none;
    }
  }

  @include viewport--xs {
    display: none;
  }
}
