.events-poster {
  padding: 45px 0 0 0;
  background-color: $color-black-2;

  @include viewport--lg {
    padding: 55px 0 0 0;
  }

  @include viewport--md {
    padding: 40px 0 0 0;
  }

  @include viewport--sm {
    padding: 50px 0 0 0;
  }

  @include viewport--xs {
    padding: 18px 0 0 0;
  }
}

.events-poster__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  h2 {
    margin: 0 0 30px 0;
    padding: 0;

    font-size: 36px;
    line-height: 37.8px;
    font-weight: 400;

    color: $color-default-white;
    text-transform: uppercase;
  }

  @include viewport--lg {
    h2 {
      margin: 0 0 52px 0;
    }
  }

  @include viewport--md {
    h2 {
      margin: 0 0 32px 0;
    }
  }

  @include viewport--sm {
    h2 {
      margin: 0 0 63px 0;
    }
  }

  @include viewport--xs {
    h2 {
      margin: 0 0 30px 0;

      font-size: 22px;
      line-height: 23.1px;
    }
  }
}

.events-poster__list {
  margin: 0 0 0 -6px;
  width: 100%;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;

  @include viewport--lg {
    margin: 0 3px 0 0;
  }

  @include viewport--sm {
    overflow: hidden;
    flex-wrap: nowrap;
  }

  @include viewport--xs {
    overflow: visible;
  }
}

.events-poster__event {
  margin: 0 2.12% 50px 0;
  width: 23.39%;

  &:last-of-type {
    margin: 0 0 50px 0;
  }

  p {
    margin: 0;
    padding: 0;

    width: 55%;

    display: flex;
    justify-content: space-between;

    font-size: 22px;
    line-height: 30px;

    color: $color-default-white;
  }

  @include viewport--lg {
    margin: 0 1.76% 50px 0;

    p {
      width: 65%;
    }
  }

  @include viewport--md {
    p {
      width: 70%;
    }
  }

  @include viewport--sm {
    margin: 0 3% 44px 0;
    min-width: 261px;
  }

  @include viewport--xs {
    margin: 0 3% 44px 0;
    min-width: 231px;

    p {
      width: 64%;
    }
  }
}

.events-poster__picture {
  margin: 0 0 36px 0;

  @include viewport--lg {
    img {
      width: 227px;
      height: 150px;
    }
  }

  @include viewport--md {
    img {
      width: 212px;
      height: 149px;
    }
  }

  @include viewport--sm {
    img {
      width: 260px;
      height: 150px;
    }
  }

  @include viewport--xs {
    img {
      width: 230px;
      height: 148px;
    }
  }
}

.events-poster__info {
  min-height: 157px;

  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;

  @include viewport--md {
    min-height: 155px;
  }
}

.events-poster__title {
  margin: 0;

  width: 100%;

  font-weight: 600;
  font-size: 22px;
  line-height: 30px;

  color: $color-default-white;
  word-wrap: break-word;

  span {
    display: block;
  }

  @include viewport--md {
    font-size: 16px;
    line-height: 21.86px;
  }
}
