.products {
  padding-top: 40px;
  padding-bottom: 18px;

  .events__showmore {
    margin: 0 auto;
    display: block;
    margin-top: 59px;
    font-size: 16px;
    line-height: 22px;
    padding: 9px 9px 7px;
    margin-bottom: 22px;
  }

  @media (max-width: 1024px) {
    padding-top: 28px;

    .container {
      padding: 0 30px;
    }

    .product-big {
      margin-bottom: 30px;
    }
  }

  @media (max-width: 960px) {
    padding-top: 32px;

    .product-big {
      margin-bottom: 19px;
    }
  }

  @media (max-width: 600px) {
    padding-top: 20px;

    .product-big {
      margin-bottom: 18px;
    }
  }

  @media (max-width: 480px) {
    .container {
      padding: 0 9px;
    }

    .product-big {
      margin-bottom: 9px;
    }
  }
}

.products__showmore {
  margin-top: 60px;
  margin-bottom: 20px;
  left: 50%;
  transform: translateX(-50%);

  @media (max-width: 1024px) {
    margin-top: 48px;
  }

  @media (max-width: 600px) {
    margin-top: 30px;
  }
}

.products__product-list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-top: -16px;

  li {
    width: calc(100% / 4 - 16px);
    margin-left: 16px;
    margin-top: 16px;
  }

  @media (max-width: 960px) {
    margin-top: -11px;

    li {
      margin-top: 11px;
    }
  }

  @media (max-width: 800px) {
    margin-top: -22px;

    li {
      width: calc(100% / 2 - 16px);
      min-height: 436px;
      margin-top: 22px;

      .product__link {
        padding: 9px 9px 18px;
      }

      .btn {
        padding-top: 8px;
        padding-bottom: 8px;
      }

      .main-products__cost {
        margin-bottom: 25px;
      }

      img {
        height: 203px;
        margin-bottom: 9px;
      }
    }
  }

  @media (max-width: 480px) {
    width: calc(100% + 18px);
    margin-left: -9px;
    margin-top: 0;

    li {
      margin: 0;
      width: 50%;
      min-height: 283px;
      margin-right: -1px;

      &:hover {
        position: relative;
        z-index: 1;
      }

      .product__link {
        padding: 9px 9px 13px;
      }

      .main-products__cost {
        margin-bottom: 7px;
      }

      .btn {
        padding-top: 0;
        padding-bottom: 0;
      }

      img {
        height: 129px;
        margin-bottom: 6px;
      }
    }
  }
}
