.sidebar {
  position: fixed;
  top: 0;
  left: -320px;
  z-index: 12;

  flex-direction: column;
  height: 100%;
  width: 320px;
  padding: 64px 0 0 0;

  color: $color-default-white;

  background-color: $color-black;
  visibility: visible;
  opacity: 1;
  overflow: auto;
  transition: all 0.6s ease;

  &--active {
    left: 0;

    visibility: visible;
    opacity: 1;
  }

  &--white {
    background-color: $color-default-white;

    .sidebar__close-btn {
      border-color: $color-gray-1;

      svg {
        fill: $color-gray-1;
      }
    }

    .sidebar__back-btn svg {
      fill: $color-gray-1;
    }

    .sidebar__header span {
      color: $color-black;
    }
  }
}

.sidebar__close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 5;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;

  border: 1px solid $color-default-white;
  border-radius: 100px;
  outline: none;

  svg {
    fill: $color-default-white;
  }

  &:active,
  &:active svg {
    border-color: $color-gold;
    fill: $color-gold;
  }
}

.sidebar__profile {
  display: flex;
  margin: 0 0 17px 19px;

  color: $color-default-white;

  p {
    display: flex;
    flex-direction: column;
    max-width: 230px;
    margin: 5px 0 0 0;
    padding: 0;
  }

  b {
    font-size: 18px;
    font-weight: 600;
    line-height: 25px;
  }

  span {
    font-size: 16px;
    line-height: 22px;
  }

  &:active {
    color: $color-gold;
  }

  &:active .user-profile {
    background-color: $color-gold;
    border-color: $color-gold;
  }
}

.sidebar__profile .user-profile {
  margin-right: 19px;
}

.sidebar__basket {
  display: flex;
  align-items: center;
  margin: 0 0 19px 19px;

  color: $color-default-white;

  b {
    max-width: 230px;
    font-size: 18px;
    font-weight: 600;
    line-height: 25px;
    word-break: break-word;
  }

  &:active {
    color: $color-gold;
  }

  &:active .round-icon {
    background-color: $color-gold;
    border-color: $color-gold;
  }
}

.sidebar__basket .round-icon {
  margin-right: 19px;
}

.sidebar .form-search {
  margin: 0 20px 16px;
}

.sidebar__site-list {
  margin: 0 0 40px 0;
  padding: 0;

  list-style: none;

  & > li > a {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 28px 10px 19px;

    font-size: 18px;
    font-weight: 600;
    line-height: 25px;
    color: $color-default-white;
    word-break: break-word;

    &::after {
      content: "";

      position: absolute;
      bottom: 0;
      left: 0;

      width: 100%;
      height: 1px;

      background-color: $color-active-gold;
      opacity: 0.4;
    }
  }
}

.sidebar__site-item {
  position: relative;

  display: flex;
  flex-direction: column;
  padding: 0;

  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  color: $color-default-white;
}

.sidebar__bottom {
  display: flex;
  flex-direction: column;
  padding: 0 19px 19px;
}

.sidebar__bottom .button-go-navigator {
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
}

.sidebar__inner {
  padding-top: 24px;
}

.sidebar__inner .sidebar__bottom a {
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  color: $color-default-white;

  &:active {
    color: $color-gold;
  }
}

.sidebar__inner .sidebar__bottom .btn {
  margin-top: 16px;
  padding-top: 17px;

  font-size: 14px;
  line-height: 23px;
  font-weight: 700;
  text-transform: uppercase;
}

.sidebar__header {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 43px;
  padding: 0 60px;

  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  text-align: center;
}

.sidebar__back-btn {
  position: absolute;
  top: 3px;
  left: 20px;

  svg {
    fill: $color-default-white;

    transform: rotate(180deg);
  }

  &:active svg {
    fill: $color-gold;
  }
}

.sidebar__inner .sidebar__site-list {
  margin-bottom: 84px;
}

.sidebar .contacts a {
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  color: $color-default-white;

  &:hover,
  &:focus {
    background: none;
  }

  &:active {
    color: $color-gold;
  }
}

.sidebar__bottom .contacts__links {
  margin-bottom: 24px;
}

.sidebar__bottom .contacts address {
  max-width: 90%;
  margin-bottom: 9px;
  line-height: 25px;
}

.sidebar .contacts__link {
  border: 0;

  &:hover,
  &:focus {
    border: 0;
  }

  @include viewport--sm {
    margin-bottom: 0;
  }
}

.sidebar .button-go-navigator {
  width: 100%;
  max-width: 250px;

  @include viewport--xs {
    padding: 6px 0 2px;
  }
}

// авторизация пользователя в сайдбаре
.sidebar__login {
  padding: 16px 20px 0;
}

.sidebar__login .sidebar__header {
  margin-bottom: 35px;
  padding-left: 0;

  text-align: left;
}

.sidebar__login-form p {
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: $color-default-black;
}

.sidebar__login-form-text {
  margin-top: 38px;
}

.sidebar__login-form button[type="submit"] {
  width: 100%;
  margin-top: 28px;
  padding-top: 16px;

  font-weight: 700;
  text-transform: uppercase;
  font-size: 14px;
}

.sidebar__login-form input {
  width: 100%;
  padding: 7px 0;

  font-size: 16px;
  color: $color-default-black;
  line-height: 23px;

  border: 0;
  border-bottom: 1px solid $color-transparent;
  outline: none;

  &::placeholder {
    color: $color-default-black;

    opacity: 0.3;
  }
}

// сайдбар для ввода кода пользователем
.sidebar__phone-code {
  padding-left: 20px;
  padding-right: 20px;

  background-color: $color-default-white;
}

.sidebar__phone-code .sidebar__header {
  margin-bottom: 52px;
}

.sidebar__phone-code .sidebar__back-btn {
  left: 0;
}
