.counter-list {
  @include list-reset;
  counter-reset: counter;
  padding-left: 36px;

  font-size: 16px;
  line-height: 22px;

  li {
    position: relative;
    margin-bottom: 43px;

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      counter-increment: counter;
      content: counter(counter);
      position: absolute;
      left: -36px;
      top: -1px;
      display: flex;
      align-items: center;
      justify-content: center;

      width: 23px;
      height: 23px;

      background-color: black;
      color: white;
      border-radius: 50%;
    }
  }
}
