.socials {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 250px;
  margin: 0 0 0 auto;
  padding: 0;
  list-style: none;

  @include viewport--sm {
    margin: 0;
  }
}

.socials__item {
  margin: 0 0 15px 15px;
}

.socials__item:first-child {
  margin: 0 0 15px;
}

.socials__link {
  position: relative;
  display: block;
  width: 45px;
  height: 45px;
  font-size: 0;
  line-height: 0;
  text-align: center;
  border: 1px solid $color-dark-gray;
  border-radius: 50%;

  &:hover,
  &:focus {
    border: 1px solid $color-gold;
  }

  &:hover svg,
  &:focus svg {
    fill: $color-gold;
  }

  &:active {
    background-color: $color-gold;
  }

  &:active svg {
    fill: $color-default-white;
  }
}

.socials__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: $color-dark-gray;
}
