.product-cards-product {
  display: flex;
  padding: 0 0 59px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);

  @include viewport--lg {
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 0 0 39px;
  }

  @include viewport--sm {
    padding: 0 0 59px;
  }

  @include viewport--xs {
    padding: 0 0 16px;
  }
}

.product-cards-product__gallery {
  position: relative;
  display: flex;
  max-width: 49.3%;
  min-width: 600px;
  margin-right: 10%;

  @include viewport--lg {
    flex-direction: column;
    align-items: flex-start;
    max-width: 49%;
    min-width: 49%;
    margin-right: 1.9%;
  }

  @include viewport--sm {
    max-width: 100%;
    margin: 0 0 41px;
  }

  @include viewport--xs {
    margin-bottom: 30px;
  }
}

.product-cards-product__gallery .gallery-top {
  max-width: 564px;

  @include viewport--lg {
    margin: 0 0 30px;
  }

  @include viewport--sm {
    margin: 0;
  }
}

.product-cards-product__gallery .thumbs-inner-wrapper {
  position: relative;
  order: -1;
  max-width: 14%;
  height: 472px;
  margin: 23px 20px 0 0;
  padding: 25px 0;

  @include viewport--lg {
    order: 0;
    max-width: 377px;
    height: auto;
    margin: 0 0 0 13px;
    padding: 0 30px 0 35px;
  }

  @include viewport--sm {
    display: none;
  }
}

.product-cards-product__gallery .gallery-thumbs {
  overflow: hidden;
  padding: 0;

  @include viewport--lg {
    height: auto;
  }
}

.product-cards-product__gallery img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-cards-product__description {
  max-width: 446px;
  font-family: $font-family--avenir;
  font-style: normal;
  color: $color-default-black;

  @include viewport--lg {
    max-width: 44%;
  }

  @include viewport--sm {
    max-width: 411px;
  }
}

.product-cards-product__info {
  margin: 0 0 28px;
  padding: 0 0 28px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);

  @include viewport--sm {
    padding: 0 0 30px;
  }

  @include viewport--xs {
    margin: 0 0 18px;
  }
}

.product-cards-product__info h1 {
  margin: 0 0 20px;
  color: $color-gray-1;

  @include viewport--xs {
    margin: 0 0 10px;
    font-size: 19px;
    line-height: 26px;
  }
}

.product-cards-product__text {
  margin: 0 0 30px;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;

  @include viewport--lg {
    margin: 0 0 10px;
  }

  @include viewport--xs {
    margin: 0 0 33px;
    font-weight: normal;
  }
}

.product-cards-product__text p {
  margin: 0;
}

.product-cards-product__price {
  display: flex;
  align-items: center;
  margin: 0 0 9px;

  @include viewport--lg {
    margin: 0 0 6px;
  }

  @include viewport--sm {
    margin: 0 0 10px;
  }

  @include viewport--xs {
    margin: 0 0 9px;
  }
}

.product-cards-product__price-big {
  font-weight: 100;
  font-size: 70px;
  line-height: 96px;
}

.product-cards-product__price-small {
  display: block;
  margin: 10px 0 0 12px;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;

  @include viewport--lg {
    margin: 10px 0 0 20px;
  }

  @include viewport--xs {
    margin: 14px 0 0 20px;
  }
}

.product-cards-product__gallery .swiper-pagination-bullets .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 0 10px;
  background: $color-black;
  opacity: 0.8;
}

.product-cards-product__gallery .swiper-pagination-bullets .swiper-pagination-bullet-active {
  width: 40px;
  height: 10px;
  border-radius: 10px;
  background: $color-default-white;
  opacity: 1;
}
