.feedback {
  padding: 69px 0 63px;
  background-color: $color-white-smoke-2;

  h3 {
    margin: 0 0 10px 0;
    padding: 0;

    font-weight: 700;
    font-size: 12px;
    line-height: 16.39px;
    color: $color-default-black;
  }

  p {
    margin: 0 0 45px 0;
    padding: 0;

    font-weight: 600;
    font-size: 22px;
    line-height: 30.05px;
    color: $color-default-black;
  }

  input {
    position: relative;
    margin: 0 10.67% 0 0;
    padding: 16px 0 8px;

    width: 100%;

    border: none;
    border-bottom: 1px solid $color-manatee;
    background-color: $color-transparent;
    box-shadow: inset 0 0 0 1000px $color-white-smoke-2;

    font-family: inherit;
    font-size: 16px;
    line-height: 21.66px;

    cursor: text;

    &:disabled {
      color: $color-suva-grey;
      border: none;
      background-color: $color-gainsboro-2;

      &::placeholder {
        color: $color-suva-grey;
      }
    }

    &:focus {
      color: $color-matternhorn;
      outline: none;
      border-bottom: 1px solid $color-black-russian;
      box-shadow: 0, 4px, 4px, rgba($color-default-black, 0.25);
    }

    &:active {
      outline: none;
      border-bottom: 1px solid $color-zambezi;
      box-shadow: 0, 4px, 4px, rgba($color-default-black, 0.25);
    }

    &::placeholder {
      color: $color-manatee;
    }
  }

  textarea {
    padding: 17px 0 13px;
    width: 100%;
    height: 46px;

    font-family: inherit;
    font-size: 16px;
    line-height: 21.66px;

    border: none;
    background-color: transparent;
    border-bottom: 1px solid $color-manatee;
    outline: none;
    overflow: hidden;

    &:focus {
      color: $color-matternhorn;
      outline: none;
      border-bottom: 1px solid $color-black-russian;
      box-shadow: 0, 4px, 4px, rgba($color-default-black, 0.25);

      &::placeholder {
        display: none;
      }
    }

    &:active {
      outline: none;
      border-bottom: 1px solid $color-zambezi;
      box-shadow: 0, 4px, 4px, rgba($color-default-black, 0.25);
    }
  }

  @include viewport--lg {
    padding: 68px 0 63px;
  }

  @include viewport--sm {
    padding: 50px 0 50px;

    p {
      margin: 0 0 30px 0;
    }

    input {
      margin: 0 0 35px 0;
    }

    textarea {
      margin: 0 0 28px 0;
    }
  }

  @include viewport--xs {
    padding: 50px 0 40px;

    input {
      margin: 0 0 35px 0;
      width: 100%;
    }

    textarea {
      margin: 0 0 28px 0;
    }
  }
}

.feedback__label {
  width: 100%;

  font-size: 16px;
  line-height: 22px;
  color: $color-suva-grey;

  span {
    position: absolute;
    top: 20px;
    left: 0;
    z-index: 1;

    transition: all linear 0.2s;
  }

  @include viewport--sm {
    width: 100%;
  }
}

.label-active {
  span {
    position: absolute;
    top: 5px;
    left: 0;
    z-index: 1;

    font-size: 11px;
    line-height: 12.4px;
  }
}

.feedback__input {
  position: relative;
  width: 31.13%;

  @include viewport--sm {
    width: 100%;
  }
}

.feedback__form {
  max-width: 915px;
  width: 100%;

  display: flex;
  flex-wrap: wrap;
}

.feedback__inputs {
  margin: 0 0 60px 0;
  width: 100%;

  display: flex;
  justify-content: space-between;

  font-size: 16px;
  line-height: 21.86px;

  @include viewport--sm {
    margin: 0 0 5px 0;
    display: block;
  }
}

.feedback__message-prompt {
  display: none;
  position: absolute;
  left: 0;
  bottom: -20px;

  font-size: 11px;
  line-height: 12.4px;
  color: $color-suva-grey;

  @include viewport--sm {
    bottom: 15px;
  }
}

.prompt-show {
  display: block;
}

.feedback__message-error {
  display: none;
  position: absolute;
  left: 0;
  bottom: -20px;

  font-size: 11px;
  line-height: 12.4px;
  color: $color-torch-red;

  @include viewport--sm {
    bottom: 15px;
  }
}

.error-show {
  display: block;
}

