.feedback__textarea {
  margin: 0 0 32px 0;
  position: relative;
  width: 100%;
  min-height: 46px;

  @include viewport--sm {
    margin: 0;
  }
}

.feedback__button {
  margin: 25px 0 0 0;

  @include viewport--sm {
    margin: 0;
    max-width: 202px;
    padding: 14px 55px 11px;
  }

  @include viewport--xs {
    margin: 12px 0 0;
  }
}

.feedback__agreement {
  width: 100%;
  display: flex;
  align-items: center;

  p {
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: $color-grey-2;
  }

  a {
    color: $color-default-black;
    font-weight: 700;
    background: linear-gradient(to right, $color-default-black) left bottom 0 no-repeat;
    background-size: 100% 1px;
  }

  input {
    padding: 0;
    width: 20px;
    height: 20px;
    margin: 0;
    border: none;
    -moz-appearance: none;
    -webkit-appearance: none;
  }

  input + label {
    padding-left: 10px;

    @include viewport--xs {
      padding-left: 20px;
      padding-right: 0;
    }
  }

  @include viewport--sm {
    label {
      margin: 2px 0 33px 0;
    }
  }
}

.feedback__agreement input + label::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 1px solid $color-suva-grey;

  @media (max-width: 585px) {
    top: 8px;
  }
}

.feedback__agreement input + label:hover::before,
.feedback__agreement input:focus + label::before {
  border: 1px solid $color-black-2;
  cursor: pointer;
}

.feedback__agreement input:not(:checked) {
  border: none;
}

.feedback__agreement input:not(:checked) + label svg {
  display: none;
}

.feedback__agreement input:checked {
  border: none;
}

.feedback__agreement input:checked + label svg {
  position: absolute;
  top: 4px;
  left: 4px;
  display: block;

  @media (max-width: 585px) {
    top: 12px;
  }
}

.feedback__agreement-invalid {
  position: relative;
  border: none;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    content: "";

    width: 20px;
    height: 20px;
    border: 1px solid $color-torch-red;
  }

  @include viewport--sm {
    top: -16px;
  }
}
