form .field-error {
  border-bottom: 1px solid $color-torch-red;
  box-shadow: 0, 4px, 4px, rgba($color-default-black, 0.25);
}

form .field-edited {
  border-bottom: 1px solid $color-suva-grey;
  color: $color-matternhorn;
}
