// если заказ не готов к оплате
.cart-total--no-ready .cart-total__sum {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;

  font-size: 22px;
  font-weight: 600;
  line-height: 30px;
  color: $color-default-black;

  @include viewport--xs {
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
  }
}

.cart-total__sum-container {
  position: relative;

  display: flex;
  justify-content: space-between;
  min-width: 480px;

  background-color: $color-default-white;
  overflow: hidden;

  @include viewport--sm {
    min-width: initial;
    width: 100%;
    max-width: 480px;

    overflow: hidden;
  }

  @include viewport--xs {
    flex-direction: column;
    align-items: center;
  }

  &::after {
    content: "..............................................................................................................................................................................................................................................................................................................................................................................................................................................................................";

    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;

    background-color: $color-default-white;

    @include viewport--xs {
      display: none;
    }
  }
}

.cart-total--no-ready .cart-total__sum-amount {
  position: relative;
  z-index: 2;

  margin-right: 20px;

  background-color: $color-default-white;

  @include viewport--xs {
    margin-right: 0;
  }

  p {
    margin: 0;
  }
}

.cart-total--no-ready .cart-total__sum-price {
  position: relative;
  z-index: 2;

  margin: 0 0 0 20px;

  background-color: $color-default-white;

  @include viewport--xs {
    margin-left: 0;
  }

  span {
    font-family: $font-family--rouble;
  }
}

.cart-total--no-ready .btn {
  margin: 21px auto 0;

  @include viewport--xs {
    display: inline-block;
    margin-top: 12px;
    padding: 8px 20px;
  }
}

.cart-total--no-ready .btn span {
  display: inline-block;
  margin: 0 3px;

  font-family: $font-family--rouble;
}

// если заказ готов к оплате
.cart-total--ready .cart-total__goods,
.cart-total--ready .cart-total__delivery {
  display: flex;
  justify-content: center;
  align-items: baseline;
  margin: 0;

  font-size: 20px;
  font-weight: 400;
  line-height: 21px;
  color: $color-default-black;

  @include viewport--xs {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
  }

  span {
    font-family: $font-family--rouble;
  }
}

.cart-total--ready .cart-total__goods {
  margin-bottom: 7px;

  @include viewport--xs {
    margin-bottom: 4px;
  }
}

.cart-total--ready .cart-total__delivery {
  margin-bottom: 9px;

  @include viewport--xs {
    margin-bottom: 5px;
  }
}

.cart-total--ready .cart-total__sum {
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 20px;
  line-height: 21px;
  font-weight: 700;
  color: $color-default-black;

  @include viewport--xs {
    font-size: 16px;
    line-height: 22px;
  }

  span {
    font-family: $font-family--rouble;
  }
}

.cart-total--ready .btn {
  margin: 30px auto 0;
  padding: 8px 119px;

  @include viewport--xs {
    width: 100%;
    padding: 8px 20px;
    margin-top: 15px;
  }
}
