.recipe-pagination {
  display: none;

  @media (max-width: 480px) {
    display: block;
  }

  & > a {
    display: block;
    margin: 0 auto 29px;
    padding: 9px 10px 8px;

    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
  }

  .events__pagination-block {
    padding: 39px 10px 15px;
  }
}
