.main-nav {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  @include viewport--lg {
    display: none;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 2px 0 0 0;
  padding: 0;

  list-style: none;
}

.main-nav__item {
  display: flex;
  align-items: center;
  margin-right: 3px;

  @include viewport--xl {
    margin-right: 0;
  }
}

.main-nav__link {
  padding: 0 10px;

  color: $color-default-white;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  text-transform: uppercase;

  border-bottom: 4px solid transparent;

  &:hover,
  &:focus {
    border-bottom: 4px solid $color-gold;
  }

  &:active,
  &--active {
    color: $color-gold;
    border-bottom: 4px solid $color-gold;
  }
}

.main-nav__dropdown .dropdown__toggle {
  position: relative;

  display: flex;
  width: 34px;
  height: 34px;

  font-size: 0;

  &::after,
  &::before,
  span {
    content: "";

    position: absolute;
    left: 50%;

    width: 19px;
    height: 1.6px;
    margin-left: -9.5px;

    border-radius: 5px;
    background-color: $color-default-white;
    transition: all 0.3s ease;
  }

  &::after {
    top: 24%;
  }

  &::before {
    bottom: 25%;
  }

  span {
    top: 50%;

    margin-top: -0.8px;
  }

  @include viewport--lg {
    display: none;
  }

  &:hover::after,
  &:hover::before,
  &:hover span,
  &:focus::after,
  &:focus::before,
  &:focus span {
    background-color: $color-gold;
  }

  &.active {
    &::after {
      top: 50%;

      width: 24px;
      margin-left: -12px;

      transform: rotate(45deg);
    }

    &::before {
      top: 50%;
      bottom: auto;

      width: 24px;
      margin-left: -12px;

      transform: rotate(-45deg);
    }

    span {
      opacity: 0;
      visibility: hidden;
    }
  }
}

// выпадающее меню
.main-nav__dropdown {
  @media (max-width: 1024px) {
    margin-left: auto;
  }

  @include viewport--lg {
    margin-left: auto;
    margin-right: 14px;
  }

  @include viewport--md {
    margin-right: 8px;
  }
}

.main-nav__dropdown .dropdown__link {
  padding: 9px 47px;
}

.main-nav__dropdown .dropdown__list {
  top: 46px;
  left: -5px;

  flex-direction: column;
  width: 315px;
  margin: 0;

  @media (max-width: 1330px) {
    left: auto;
    right: -30px;
  }
}

.main-nav__dropdown .dropdown__bottom {
  padding: 26px 47px 23px;
}
