.page-contacts {
  @include viewport--lg {
    border-bottom: 28px solid $color-black;
  }

  @include viewport--sm {
    border: none;
  }
}

.page-contacts__map {
  margin-top: 39px;
  margin-bottom: 49px;

  @media (max-width: 1135px) {
    padding: 0 30px;
  }

  @include viewport--lg {
    margin-bottom: 40px;
  }

  @include viewport--sm {
    margin-top: 28px;
    margin-bottom: 40px;
  }

  @include viewport--xs {
    margin-bottom: 28px;
    padding: 0 20px;
  }
}

.page-contacts__info {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: 46px;

  @media (max-width: 1135px) {
    padding: 0 30px;
  }

  @include viewport--lg {
    margin-bottom: 4px;
  }

  @include viewport--sm {
    flex-direction: column;
    margin: 0 0 35px;
  }

  @include viewport--xs {
    margin: 0 0 16px;
    padding: 0 20px;
  }
}

.page-contacts__info address {
  @include viewport--lg {
    margin: 0;
  }

  @include viewport--sm {
    max-width: 100%;
  }
}

.page-contacts__info .h3 {
  @include viewport--xs {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
  }
}

.page-contacts__info .contacts__links {
  margin: 0 0 23px;

  @include viewport--sm {
    margin: 0 0 24px;
  }

  @include viewport--xs {
    margin: 0 0 18px;
  }
}

.page-contacts__info .contacts__links .h3 {
  line-height: 25px;
}

.page-contacts__contacts {
  margin-right: 20px;

  @include viewport--lg {
    max-width: 33.3%;
    margin-right: 22px;
    margin-bottom: 10px;
  }

  @include viewport--sm {
    max-width: 100%;
    margin-bottom: 30px;
  }

  @include viewport--xs {
    margin: 0 0 24px;
  }
}

.page-contacts__contacts .contacts__link {
  @include viewport--lg {
    margin-right: 5px;
  }
}

.page-contacts__schedule {
  @include viewport--lg {
    max-width: 30%;
  }

  @include viewport--sm {
    max-width: 100%;
    margin-bottom: 28px;
  }
}

.page-contacts__socials {
  justify-content: flex-end;
  flex-grow: 1;

  @include viewport--sm {
    justify-content: flex-start;
  }
}
