.swiper-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.thumbs-inner-wrapper .swiper-container {
  height: 100%;
}

.swiper-slide {
  background-size: cover;
  background-position: center;
}

.gallery-top {
  width: 100%;
  height: 100%;
}

.gallery-top img {
  @include viewport--lg {
    max-width: 440px;
  }

  @include viewport--sm {
    max-width: 540px;
  }
}

.gallery-top .swiper-pagination {
  display: none;

  @include viewport--sm {
    display: block;
    bottom: 2px;
  }
}

.thumbs-inner-wrapper {
  position: relative;
}

.thumbs-inner-wrapper .swiper-button-prev,
.thumbs-inner-wrapper .swiper-button-next {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 20px;
  height: 12px;
  z-index: 2;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.8;
    cursor: pointer;
  }

  &:active {
    opacity: 0.5;
  }

  @include viewport--lg {
    transform: rotate(270deg);
    top: 43px;
    left: 0;
  }
}

.thumbs-inner-wrapper .swiper-button-next {
  left: calc(50% - 10px);
  transform: rotate(180deg);
  top: auto;
  bottom: 0;

  @include viewport--lg {
    transform: rotate(90deg);
    top: 43px;
    left: auto;
    right: 0;
  }
}

.thumbs-inner-wrapper .swiper-button-prev::after,
.thumbs-inner-wrapper .swiper-button-next::after {
  content: "";
}


.gallery-thumbs {
  padding: 10px 0;
  box-sizing: border-box;
}

.gallery-thumbs .swiper-slide {
  max-width: 96px;

  @include viewport--lg {
    max-width: 60px;
  }
}
