.recipe-products {
  padding-top: 61px;
  padding-bottom: 40px;

  h2 {
    margin: 0;
    font-weight: normal;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
  }

  .recipe-products__button {
    padding: 8px 10px;
    display: block;
    width: 176px;
    margin: 0 auto;
    margin-top: 42px;

    font-weight: 500;
    font-size: 16px;
    line-height: 22px;

    @media (max-width: 800px) {
      display: none;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 30px;

    .products {
      padding-top: 20px;
    }

    .recipe-products__button {
      margin-top: 22px;
    }

    .container {
      padding: 0 30px;
    }
  }

  @media (max-width: 960px) {
    padding-top: 40px;
    padding-bottom: 50px;
  }

  @media (max-width: 800px) {
    padding-top: 29px;
    padding-bottom: 50px;

    .products {
      padding-top: 31px;
    }
  }

  @media (max-width: 480px) {
    padding-bottom: 30px;

    h2 {
      text-align: left;
      padding: 0 20px;
    }

    .products {
      padding-top: 13px;

      .products__product-list {
        width: 100%;
        margin-left: 0;
      }
    }

    .container {
      padding: 0;
    }
  }
}
