.contacts {
  margin: 0 0 20px;

  @include viewport--lav {
    margin: 0 0 45px;
  }

  @include viewport--av {
    margin: 0 0 13px;
  }
}

.contacts__links {
  margin: 0 0 23px;

  @include viewport--lav {
    margin: 0 0 24px;
  }

  @include viewport--xs {
    margin: 0 0 22px;
  }
}

.contacts p,
.contacts address {
  margin: 0;
  max-width: 330px;
  color: $color-default-white;
}

.contacts p {
  @include viewport--av {
    line-height: 18px;
  }
}

.contacts__link {
  display: inline-block;
  margin: 0 40px 0 0;
  line-height: 21px;
  color: $color-default-white;
  background: linear-gradient(to right, $color-default-white) left bottom 0 no-repeat;
  background-size: 0 1px;

  &:hover,
  &:focus {
    outline: none;
    background-size: 100% 1px;
    cursor: pointer;
  }

  &:active {
    color: $color-suva-grey;
  }
}

.contacts span {
  white-space: nowrap;
}

.contacts--black p {
  color: $color-default-black;
}

.contacts--black .contacts__link,
.contacts--black address {
  color: $color-default-black;
}

.contacts--black .contacts__link {
  background: linear-gradient(to right, $color-default-black) left bottom 0 no-repeat;
  background-size: 0 1px;

  &:hover,
  &:focus {
    background-size: 100% 1px;
  }

  &:active {
    color: $color-suva-grey;
  }
}

