.corners-cards {
  width: 100%;
  margin-bottom: 64px;

  @include viewport--lg {
    margin-bottom: 23px;
  }

  @include viewport--sav {
    margin-bottom: 18px;
  }

  @include viewport--xs {
    margin-bottom: 23px;
  }
}

.corners-cards .card-intro__description p {
  margin-bottom: 25px;
}

.corners-cards__menu {
  @include viewport--lg {
    margin-bottom: 16px;
  }
}

.corners-cards__products {
  padding-top: 15px;

  @include viewport--lg {
    padding-top: 11px;
  }

  @include viewport--sav {
    padding-top: 2px;
  }

  @include viewport--xs {
    padding-top: 8px;
  }
}

.corners-cards__products h2 {
  margin-bottom: 32px;
  text-transform: uppercase;

  @include viewport--lg {
    margin-top: 12px;
  }

  @include viewport--xs {
    margin-bottom: 12px;
  }
}

.corners-cards__products-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  width: calc(100% + 16px);

  @include viewport--xs {
    width: 100%;
  }
}

.corners-cards__products-item {
  width: calc(25% - 16px);
  min-height: 335px;
  margin-right: 16px;
  margin-bottom: 16px;

  @include viewport--sav {
    width: calc(50% - 16px);
    margin-bottom: 20px;
  }

  @include viewport--xs {
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.corners-cards__products-item--big {
  width: calc(100% - 16px * 2 - (25% - 16px));

  @include viewport--sav {
    width: 100%;
  }
}

.corners-cards__products-item--big .main-product__link {
  flex-direction: row;
  justify-content: flex-start;
  padding-bottom: 9px;
}

.corners-cards__products-item--big .main-product__block-description {
  flex-grow: 0;
  max-width: 31%;
  padding-top: 26px;
  padding-left: 20px;

  @include viewport--lg {
    padding-top: 28px;
  }

  @include viewport--sav {
    max-width: 50%;
  }
}

.corners-cards__products-item--big .button-cart {
  margin-bottom: 6px;
}

.corners-cards__products--standart li {
  &:nth-child(n + 5) {
    @include viewport--sav {
      display: none;
    }
  }
}

.corners-cards__products--notitle h2 {
  @include viewport--sav {
    display: none;
  }
}

.corners-cards__products--big li {
  &:nth-child(n + 4) {
    @include viewport--sav {
      display: none;
    }
  }
}

.corners-cards__products--big {
  @include viewport--xs {
    display: none;
  }
}

