.recipe-card {
  width: calc(100% / 4 - 15px);
  margin-left: 15px;
  margin-top: 16px;

  border: 1px solid $color-gainsboro;
  background-color: white;

  &.big {
    width: calc((100% / 4) * 3 - 15px);

    a {
      display: flex;
      padding-bottom: 9px;
    }

    h3 {
      font-weight: 600;
      font-size: 22px;
      line-height: 30px;
    }

    img {
      height: 352px;
      margin-bottom: 0;
      width: 65.8%;
    }

    picture {
      width: 65.8%;

      img {
        width: 100%;
      }
    }

    .recipe-card__text-wrapper {
      padding: 28px 7px 5px 19px;
    }

    @media (max-width: 1024px) {
      img {
        width: 59.3%;
      }

      picture {
        width: 59.3%;

        img {
          width: 100%;
        }
      }
    }

    @media (max-width: 960px) {
      a {
        padding: 12px 10px 12px 8px;
      }

      img {
        width: 56.4%;
        height: 473px;
      }

      picture {
        width: 56.4%;

        img {
          width: 100%;
        }
      }

      .recipe-card__text-wrapper {
        padding: 24px 4px 5px 20px;
      }

      h3 {
        margin-bottom: 11px;
      }
    }

    @media (max-width: 800px) {
      width: 100%;

      a {
        padding-top: 7px;
        padding-bottom: 7px;
      }

      img {
        width: 45.4%;
        height: 289px;
      }

      picture {
        width: 45.4%;

        img {
          width: 100%;
        }
      }

      .recipe-card__text-wrapper {
        padding-top: 30px;
      }

      h3 {
        margin-bottom: 11px;
      }
    }

    @media (max-width: 480px) {
      a {
        padding: 0;
        padding-bottom: 13px;
        flex-direction: column;
      }

      img {
        width: 100%;
        height: 187px;
        margin-bottom: 13px;
      }

      .recipe-card__text-wrapper {
        padding: 0;
      }

      picture {
        width: 100%;

        img {
          width: 100%;
        }
      }

      h3 {
        margin-bottom: 13px;
        font-size: 16px;
        line-height: 22px;
        font-weight: bold;
      }

      h3,
      p {
        padding: 0 13px;
      }
    }
  }

  a {
    display: block;
    width: 100%;
    height: 100%;
    padding: 9px;
    padding-bottom: 15px;
  }

  img {
    height: 178px;
    width: 100%;
    object-fit: cover;
    margin-bottom: 13px;
    flex-shrink: 0;

    transition: opacity $default-transition-settings;
  }

  picture {
    flex-shrink: 0;
  }

  h3 {
    margin: 0;
    margin-bottom: 14px;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;

    color: $color-gray-1;
  }

  p {
    margin: 0;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;

    color: black;
  }

  &:hover {
    img {
      opacity: 0.7;
    }
  }

  @media (max-width: 960px) {
    margin-top: 20px;

    a {
      padding: 11px 6px 13px;
    }

    img {
      height: 240px;
      margin-bottom: 17px;
    }

    h3 {
      margin-bottom: 3px;
    }
  }

  @media (max-width: 800px) {
    width: calc(100% / 2 - 15px);

    h3 {
      margin-bottom: 26px;
    }
  }

  @media (max-width: 480px) {
    width: 100%;

    a {
      padding: 0;
      padding-bottom: 13px;
    }

    img {
      height: 187px;
      margin-bottom: 13px;
    }

    h3 {
      margin-bottom: 13px;
    }

    h3,
    p {
      padding: 0 13px;
    }
  }
}
