.navigation-arrow {
  position: relative;
  width: 47px;
  height: 47px;

  border: 1px solid white;
  background-color: transparent;
  border-radius: 50%;
  cursor: pointer;

  transition: background-color $default-transition-settings, border $default-transition-settings;

  svg {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-60%, -50%);
    transition: stroke $default-transition-settings;
    stroke: white;
  }

  &:focus {
    outline: none;

    border: 1px solid $color-active-gold;
    background-color: $color-active-gold;

    svg {
      stroke: white;
    }
  }

  &:hover {
    border: 1px solid $color-active-gold;
    background-color: $color-active-gold;

    svg {
      stroke: white;
    }
  }

  &--black {
    border: 1px solid black;

    svg {
      stroke: black;
    }
  }

  &--gold {
    border: 1px solid $color-gold;

    svg {
      stroke: $color-gold;
    }
  }

  @media (max-width: 1024px) {
    width: 34px;
    height: 34px;

    svg {
      width: 10px;
    }
  }

  @media (max-width: 600px) {
    width: 40px;
    height: 40px;
  }
}

.navigation-arrow--next {
  transform: rotate(-180deg);
}
