.ordering {
  padding-top: 30px;
}

.ordering .inner-header {
  margin-bottom: 54px;

  @include viewport--lg {
    margin-bottom: 46px;
  }

  @include viewport--sm {
    align-items: flex-start;
  }

  @include viewport--xs {
    margin-bottom: 10px;
  }
}

.ordering__form-wrapper .container {
  max-width: 778px;

  @include viewport--lg {
    max-width: 730px;
  }
}

.ordering .inner-menu {
  max-width: 778px;
  margin-bottom: 20px;
  padding: 0 50px;

  background-color: $color-transparent;

  @include viewport--lg {
    max-width: 730px;
    padding: 0 30px;
  }

  @include viewport--sm {
    margin-bottom: 10px;
  }

  @include viewport--xs {
    padding: 0 20px;
  }
}

.ordering .inner-menu__list {
  width: 100%;
  grid-template-columns: repeat(3, 165px);
  grid-column-gap: 3.3%;

  @include viewport--sm {
    grid-template-columns: repeat(3, 170px);
    grid-column-gap: 2.7%;
  }

  @include viewport--xs {
    grid-template-columns: repeat(3, 120px);
    grid-column-gap: 3.8%;
  }
}

.ordering .inner-menu__item {
  max-width: initial;

  @include viewport--lg {
    max-width: initial;
    width: 165px;
  }

  @include viewport--sm {
    width: 170px;
  }

  @include viewport--xs {
    width: 120px;
  }
}

.ordering .inner-menu__link {
  padding: 16px 8px;

  @include viewport--lg {
    padding: 15px 8px;
  }

  @include viewport--xs {
    padding: 7px 8px;
  }
}

.ordering .tab-content {
  margin-bottom: 88px;

  @include viewport--lg {
    margin-bottom: 46px;
  }

  @include viewport--sm {
    margin-bottom: 99px;
  }

  @include viewport--xs {
    margin-bottom: 18px;
  }
}

.ordering .tab-trigger--active {
  color: $color-default-white;

  background-color: $color-active-gold;
  border: 1px solid $color-active-gold;
}

.ordering__form {
  margin-bottom: 19px;

  @include viewport--xs {
    margin-bottom: 17px;
  }
}
