.intro-info__title {
  margin: 0 0 20px 0;

  max-width: 555px;

  h2 {
    margin: 0;
    padding: 0;

    width: 100%;

    font-size: 36px;
    line-height: 48px;
    font-weight: 400;
  }

  @include viewport--lg {
    h2 {
      margin: 0 0 35px 0;
      padding: 0 0 0 2px;
    }
  }

  @include viewport--sm {
    padding: 0;
    width: 79%;

    h2 {
      margin: 0 0 37px 0;
      padding: 0;
    }
  }

  @include viewport--xs {
    margin: 0 0 30px 0;
    width: 100%;

    h2 {
      margin: 0 0 30px 0;

      font-size: 22px;
      line-height: 23.1px;
    }
  }
}

.intro-info__description {
  max-width: 459px;
  width: 100%;

  p {
    margin: 0 0 30px 0;
    padding: 0;

    font-size: 18px;
    line-height: 24.65px;

    &:last-of-type {
      margin: 0;
    }
  }

  @include viewport--lg {
    padding: 0 0 0 2px;
    width: 100%;
  }

  @include viewport--sm {
    padding: 0;
    width: 84%;
  }

  @include viewport--xs {
    width: 100%;

    p {
      margin: 0 0 22px 0;

      font-size: 16px;
      line-height: 22px;
    }
  }
}
