.footer {
  width: 100%;
  min-height: 100px;
  font-family: $font-family--avenir;
  background-color: $color-black;
}

.footer__content {
  display: flex;
  flex-direction: column;
  padding: 55px 30px 16px;

  @include viewport--lav {
    padding: 30px 35px 15px;
  }

  @include viewport--av {
    padding: 30px 33px 15px;
  }

  @include viewport--xs {
    padding: 31px 33px 17px;
  }
}

.footer__main {
  display: flex;
  justify-content: space-between;

  @include viewport--lav {
    justify-content: flex-start;
  }

  @include viewport--av {
    flex-direction: column;
    margin: 0 auto;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  min-width: 81%;

  @include viewport--lav {
    flex-wrap: wrap;
    min-width: auto;
    max-width: 675px;
  }

  @include viewport--av {
    max-width: 535px;
  }
}

.footer__logo-wrapper {
  margin-right: 10px;

  @include viewport--lav {
    margin-right: 7.3%;
    padding-top: 2px;
  }

  @include viewport--lav {
    margin-bottom: 38px;
  }
}

.footer__logo svg {
  @include viewport--lav {
    width: 150.4px;
    height: 40px;
  }

  @include viewport--av {
    width: 141px;
    height: 37.35px;
  }

  @include viewport--xs {
    width: 141px;
    height: 36.5px;
  }
}

.footer__menu {
  margin: 0 50px 0 10px;

  @include viewport--lav {
    margin-bottom: 27px;
  }

  @include viewport--av {
    margin: 0 10px 29px 0;
  }

  @include viewport--xs {
    margin-bottom: 10px;
  }
}

.footer__menu-list {
  columns: 2;
  column-gap: 63px;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;

  @include viewport--lav {
    columns: 1;
    column-gap: 0;
  }

  @include viewport--av {
    columns: 2;
    column-gap: 63px;
  }

  @include viewport--xs {
    columns: 1;
    column-gap: 0;
  }
}

.footer__menu-list li {
  margin: 0 0 14px;
  break-inside: avoid;
}

.footer__link {
  display: inline-block;
  padding: 4px 0 0;
  color: $color-default-white;
  background: linear-gradient(to right, $color-default-white) left bottom 0 no-repeat;
  background-size: 0 1px;

  &:hover,
  &:focus {
    outline: none;
    background-size: 100% 1px;
    cursor: pointer;
  }

  &:active {
    color: $color-suva-grey;
  }
}

.footer__contacts-wrapper {
  margin: 10px 10px 0;

  @include viewport--lav {
    margin: 3px 25px 0 10px;
  }

  @include viewport--av {
    margin-left: 0;
  }
}

.footer__contacts-wrapper .footer__contacts {
  margin: 0 0 21px;

  @include viewport--lav {
    margin: 0 0 44px;
  }

  @include viewport--av {
    margin: 0 0 14px;
  }

  @include viewport--xs {
    margin: 0 0 18px;
  }
}

.footer__contacts .contacts__links {
  @include viewport--xs {
    margin-bottom: 27px;
  }
}

.footer__contacts .contacts__link {
  @include viewport--av {
    line-height: 24px;
  }
}

.footer .footer__contacts address {
  @include viewport--av {
    max-width: 100%;
  }
}

.footer__go-navigator {
  margin: 0;
}

.footer__service-menu {
  width: 95%;
  margin: 0 0 26px;

  @include viewport--lav {
    margin: 0 0 18px;
  }
}

.footer__service-menu-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer__link--service {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
}

.footer__service-menu--mobile {
  display: none;

  @include viewport--av {
    display: block;
    max-width: 164px;
    margin-top: 5px;
  }

  @media (max-width: 548px) {
    max-width: 100%;
    margin-bottom: 38px;
  }
}

.footer__service-menu--mobile .footer__link--service {
  display: inline;
  line-height: 22px;
}

.footer__form-wrapper {
  @include viewport--lav {
    margin: 0 10px 10px;
  }

  @include viewport--av {
    display: none;
  }
}

.footer__copyright {
  margin: 18px auto 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  color: $color-default-white;

  @include viewport--lav {
    max-width: 585px;
  }

  @include viewport--av {
    margin-top: 50px;
  }

  @include viewport--xs {
    min-width: 286px;
    margin-left: -15px;
    margin-top: 35px;
  }
}
