.card-intro {
  padding: 10px 30px 51px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $color-black-2;

  @include viewport--lg {
    padding-bottom: 41px;
  }

  @include viewport--sm {
    padding-top: 0;
    padding-bottom: 45px;
  }

  @include viewport--xs {
    padding: 17px 20px 30px;
  }
}

.card-intro--black {
  padding: 10px 0 59px;
  background-color: $color-default-white;

  @include viewport--lg {
    padding: 0 0 41px;
  }

  @include viewport--sm {
    padding-bottom: 50px;
  }

  @include viewport--xs {
    padding-bottom: 29px;
  }
}

.card-intro h1 {
  margin: 0 auto 53px;

  width: 100%;
  max-width: 670px;

  color: $color-active-gold;
  text-align: center;
  text-transform: uppercase;

  @include viewport--sm {
    margin: 0 0 40px;
    text-align: left;
  }

  @include viewport--xs {
    margin: 0 0 14px;
  }
}

.card-intro__wrapper {
  display: flex;
  max-width: 1146px;
  margin: 0 auto;
  box-sizing: border-box;
  color: $color-default-white;

  @include viewport--lg {
    align-items: center;
  }

  @include viewport--sm {
    flex-direction: column;
  }
}

.card-intro--black .card-intro__wrapper {
  color: $color-default-black;
}

.card-intro--black h2 {
  margin: 2px 0 20px;
  color: $color-gray-1;

  @include viewport--lg {
    margin-top: 0;
  }

  @include viewport--xs {
    margin: 0 0 10px;
    font-size: 19px;
    line-height: 26px;
  }
}

.card-intro__image {
  max-width: 49%;
  margin-right: 24px;

  @include viewport--lg {
    margin-right: 16px;
  }

  @include viewport--sm {
    max-width: 100%;
    margin-right: 0;
  }
}

.card-intro img {
  object-fit: cover;
  clip-path: polygon(22% 0.5%, 99% 38%, 82% 99.5%, 0.5% 79.8%);
}

.card-intro__description {
  max-width: 49%;
  padding-top: 82px;

  @include viewport--lg {
    padding-top: 0;
    padding-bottom: 7%;
    max-width: 45.8%;
  }

  @include viewport--sm {
    max-width: 100%;
    padding-top: 40px;
    padding-bottom: 0;
  }

  @include viewport--xs {
    padding-top: 29px;
    padding-bottom: 0;
  }
}

.card-intro__description p {
  width: 100%;
  margin: 0 0 25px 0;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;

  @include viewport--xs {
    margin: 0 0 20px 0;
  }
}

.card-intro__description time {
  display: block;
  margin: 0 0 25px 0;
  font-size: 22px;
  line-height: 30px;

  @include viewport--xs {
    margin: 0 0 20px 0;
  }
}

.card-intro p.h4 {
  margin: 0;
  font-weight: normal;
}

.card-intro ul {
  margin: 0;
  padding-left: 0;
  list-style-position: inside;

  @include viewport--xs {
    margin-bottom: 24px;
  }
}

.card-intro li {
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
}

.card-intro li::marker {
  margin-right: 2px;
}

.card-intro__price {
  display: flex;
  align-items: center;
  margin: 36px 0 4px;

  @include viewport--lg {
    margin: 36px 0 6px;
  }

  @include viewport--sm {
    margin-bottom: 0px;
  }

  @include viewport--xs {
    margin: 0 0 9px;
  }
}

.card-intro__price-big {
  font-weight: 100;
  font-size: 70px;
  line-height: 96px;
}

.card-intro__price-small {
  display: block;
  margin: 10px 0 0 64px;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;

  @include viewport--xs {
    margin: 14px 0 0 20px;
  }
}

.restaurants-cards--corner .card-intro {
  padding-bottom: 50px;

  @include viewport--lg {
    padding-bottom: 40px;
  }

  @include viewport--sm {
    padding-bottom: 23px;
  }
}

.restaurants-cards--corner .card-intro__description {
  @include viewport--lg {
    padding-right: 5px;
    padding-bottom: 5%;
  }

  @include viewport--sm {
    padding-right: 0;
  }
}

.restaurants-cards--corner .card-intro p {
  margin-bottom: 23px;
}

.restaurants-cards--corner .card-intro p:last-child {
  margin-bottom: 0;
}

.meal-cards .card-intro__wrapper {
  justify-content: space-between;
  box-sizing: border-box;
  width: 100%;
  max-width: 1162px;
  margin: 0;

  @include viewport--lg {
    align-items: flex-start;
  }
}

.meal-cards .card-intro__image {
  margin-right: 10px;

  @include viewport--sm {
    max-width: 100%;
    margin-right: 0;
  }

  @include viewport--xs {
    margin-bottom: 30px;
  }
}

.meal-cards .card-intro img {
  clip-path: none;
}


.meal-cards .card-intro__description {
  max-width: 465px;
  padding-top: 0;

  @include viewport--lg {
    max-width: 49%;
    padding-bottom: 0;
  }

  @include viewport--sm {
    max-width: 100%;
    padding-top: 44px;
  }

  @include viewport--xs {
    padding-top: 0;
  }
}

