.user-nav {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  list-style: none;

  @include viewport--lg {
    align-items: center;
    min-width: 75px;
    margin: 0 7px 0 16px;
  }

  @include viewport--sm {
    display: none;
  }
}

.user-nav__item {
  margin-right: 7px;

  &:last-child {
    margin-right: 0;
  }
}

// выпадающее меню профиля
.user-nav__dropdown .dropdown__list {
  top: 62px;
  left: auto;
  right: -30px;

  width: 231px;

  @include viewport--lg {
    top: 86px;
    right: -40px;
  }
}

.user-nav__dropdown ul {
  padding-top: 24px;

  li a {
    padding: 9px 30px;

    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: $color-default-black;

    &:hover,
    &:focus {
      background-color: $color-white-smoke;
    }
  }
}

.user-nav__dropdown .dropdown__bottom {
  margin-top: 24px;
  padding-right: 18px;

  a {
    width: 100%;

    span {
      max-width: 110px;
    }
  }
}

.user-nav__dropdown .round-icon svg {
  fill: none;
}

.user-nav__dropdown .round-icon:hover svg,
.user-nav__dropdown .round-icon:focus svg {
  stroke: $color-gold;
}

.user-nav__dropdown .round-icon:active svg {
  stroke: $color-default-white;
}

.user-nav__profile svg {
  fill: none;
  stroke: $color-default-white;
}

.user-nav__profile:hover svg,
.user-nav__profile:focus svg {
  stroke: $color-gold;
}

.user-nav__profile:active svg {
  stroke: $color-default-white;
}

.user-nav-toggle--tablet {
  display: none;

  outline: none;

  @include viewport--lg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    margin-left: auto;
  }

  svg {

    stroke: $color-default-white;
  }

  &:hover svg,
  &:focus svg {
    stroke: $color-gold;
  }
}
