.my-orders {
  margin: 30px 0;
}

.my-orders .inner-header {
  margin-bottom: 55px;

  @include viewport--xs {
    margin-bottom: 10px;
  }
}

.my-orders .inner-menu {
  margin-bottom: 19px;
  padding-top: 0;

  background-color: $color-transparent;

  @include viewport--sm {
    margin-bottom: 9px;
  }
}

.my-orders .inner-menu__list {
  grid-template-columns: repeat(4, 1fr);

  @include viewport--lg {
    width: 100%;
  }

  @media (max-width: 830px) {
    grid-auto-columns: 185px;
    grid-auto-flow: column;
    grid-template-columns: repeat(4, minmax(185px, 1fr));
  }
}

.my-orders .inner-menu__item {
  width: auto;
}

.my-orders .inner-menu__link {
  padding: 16px 8px;

  @include viewport--xs {
    padding: 7px 8px 8px;
  }
}

.my-orders__item {
  margin-bottom: 21px;
  padding: 16px 26px 8px 35px;

  border: 1px solid rgba($color-grey-2, 0.2);

  @include viewport--lg {
    padding-right: 10px;
  }

  @include viewport--sm {
    padding: 16px 8px 9px 12px;
  }

  @include viewport--xs {
    margin-bottom: 10px;
    padding: 0 0 18px;
  }
}

.my-orders__item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;

  @include viewport--xs {
    position: relative;
  }
}

.my-orders__item-address {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 20px 20px 21px;

  font-style: normal;

  background-color: rgba($color-gainsboro-3, 0.2);

  @include viewport--sm {
    padding: 10px 20px;
  }

  @include viewport--xs {
    flex-direction: column;
    align-items: flex-start;
    padding: 14px 29px 14px 12px;
  }
}

.my-orders__item-address p {
  margin: 0 20px 0 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: $color-default-black;

  @include viewport--xs {
    margin: 0 0 14px 0;
  }
}

.my-orders__item-address a {
  margin: 0 65px 0 auto;

  font-size: 16px;
  line-height: 18px;
  color: $color-eclipse;

  @include viewport--sm {
    margin-right: 0;
  }

  @include viewport--xs {
    margin: 0;
  }

  &:hover,
  &:focus {
    color: $color-gold;
  }

  &:active {
    color: $color-active-gold;
  }
}

.my-orders__remove-btn {
  width: 35px;
  height: 35px;
  margin-left: 9px;
  padding: 10px;

  @include viewport--lg {
    margin-left: 16px;
  }

  @include viewport--sm {
    margin-left: 3px;
  }

  @include viewport--xs {
    position: absolute;
    top: 0;
    right: 0;

    margin: 0;
  }

  &:hover svg,
  &:focus svg {
    fill: $color-gold;
  }

  &:active svg {
    fill: $color-active-gold;
  }
}

.my-orders__item-info {
  position: relative;

  display: flex;
}

.my-orders__item-text {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  padding-left: 4px;

  font-family: $font-family--myseo;
  font-weight: 300;
  font-size: 18px;
  line-height: 36px;
  color: $color-default-black;

  &--active {
    display: none;
  }

  @include viewport--xs {
    padding: 3px 0 0 12px;

    line-height: 22px;
  }
}

.my-orders__item-text p {
  margin: 0 9px 0 0;

  @include viewport--xs {
    margin: 0 0 10px;
  }
}

.my-orders__item-text b {
  margin: 0 12px 0 14px;

  font-weight: 500;

  span {
    font-family: $font-family--rouble;
  }

  @include viewport--xs {
    margin: 0;
  }
}

.my-orders__accordion-btn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 35px;
  height: 35px;
  padding: 10px;

  &--active {
    position: absolute;
    top: 0;
    right: 0;

    svg {
      transform: rotateX(-180deg);
    }
  }

  svg {
    stroke: $color-default-black;
  }

  &:hover svg,
  &:focus svg {
    stroke: $color-gold;
  }

  &:active svg {
    stroke: $color-active-gold;
  }
}

.my-orders__item-content {
  display: none;
  width: 100%;
  margin: 19px 0 13px;
  padding: 0 45px 0 0;

  &--opened {
    display: block;
  }

  @include viewport--sm {
    margin-top: 0;
  }

  @include viewport--xs {
    margin-bottom: 0;
    padding: 5px 35px 0 12px;
  }
}

.my-orders__table-wrapper {
  margin-bottom: 84px;

  @include viewport--sm {
    margin-bottom: 21px;
  }
}

.my-orders__table {
  font-size: 18px;
  font-family: $font-family--myseo;
  font-weight: 300;
  line-height: 36px;
  color: $color-default-black;

  border-collapse: collapse;

  @include viewport--sm {
    display: flex;
  }

  @include viewport--xs {
    line-height: 22px;
  }
}

.my-orders__table tbody {
  @include viewport--sm {
    width: 100%;
  }
}

.my-orders__table tr {
  @include viewport--sm {
    display: flex;
    flex-wrap: wrap;
  }

  @include viewport--xs {
    align-items: baseline;
    margin-bottom: 40px;
  }
}

.my-orders__table td {
  padding: 0 20px 0 0;

  vertical-align: top;
  word-break: break-word;

  &:last-child {
    padding-right: 0;
  }

  @include viewport--sm {
    padding: 0;
  }
}

.my-orders__table-name {
  min-width: 467px;

  @media (max-width: 820px) {
    min-width: initial;
  }

  @include viewport--sm {
    width: 78%;
    margin-right: 3%;
  }

  @include viewport--xs {
    width: 100%;
    margin: 0 0 10px;
  }
}

.my-orders__table-amount {
  min-width: 132px;

  @media (max-width: 820px) {
    min-width: 100px;
  }

  @include viewport--sm {
    width: 19%;
    min-width: initial;
  }

  @include viewport--xs {
    margin-right: 5%;
    width: 52%;
  }
}

.my-orders__table-price {
  min-width: 100px;

  @include viewport--sm {
    width: 100%;
    min-width: initial;
  }

  @include viewport--xs {
    width: 43%;
  }
}

.my-orders__table-price .rouble {
  font-size: 18px;
  font-weight: 300;
  line-height: 22px;
}

.my-orders__table-delivery {
  margin-bottom: 8px;
}

.my-orders__table-delivery .my-orders__table-name {
  @include viewport--xs {
    width: 52%;
    margin: 0 5% 0 0;
  }
}

.my-orders__table-total {
  margin-bottom: 6px;
}

.my-orders__table-total .my-orders__table-name,
.my-orders__table-total .my-orders__table-price {
  padding-top: 35px;

  font-weight: 500;

  @include viewport--sm {
    padding-top: 0;
  }
}

.my-orders__table-total .my-orders__table-name {
  @include viewport--xs {
    width: 52%;
    margin: 0 5% 0 0;
  }
}

.my-orders__table-total .my-orders__table-price {
  @include viewport--sm {
    width: 19%;
  }

  @include viewport--sm {
    width: auto;
  }
}

.my-orders__btn-repeat {
  padding: 7px 47px 7px;

  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: $color-gray-1;
}
