.breadcrumbs {
  margin: 0;

  &--bottom {
    margin-bottom: 60px;

    @include viewport--lg {
      margin-bottom: 46px;
    }

    @include viewport--sm {
      margin-bottom: 38px;
    }

    @include viewport--xs {
      margin-bottom: 22px;
    }
  }

  &--wide {
    .breadcrumbs-list {
      margin: 0;
    }
  }

  &--white .breadcrumbs__link {
    color: $color-default-white;

    &:hover,
    &:focus {
      text-decoration: underline;
      color: $color-suva-grey;
    }

    &:active {
      text-decoration: none;
      color: $color-suva-grey;
    }
  }
}

.breadcrumbs-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 170px;
  padding: 0;
  list-style: none;

  @include viewport--sm {
    justify-content: flex-start;
    margin: 0;
  }
}

.breadcrumbs__item {
  display: inline;
}

.breadcrumbs__link {
  position: relative;
  display: inline-block;
  padding-right: 18px;
  font-family: $font-family--avenir;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  text-transform: uppercase;
  color: $color-manatee;
  transition: 0.3s ease;

  &::after {
    position: absolute;
    content: ">";
    top: 0;
    right: 4px;
  }

  &:hover,
  &:focus {
    text-decoration: underline;
    color: $color-black;
  }

  &:active {
    text-decoration: none;
    color: $color-black;
  }

  @include viewport--xs {
    justify-content: flex-start;
    padding-right: 14px;
    font-size: 12px;
    line-height: 16px;
  }
}

.breadcrumbs__item:last-child .breadcrumbs__link {
  padding-right: 0;

  &::after {
    content: "";
  }
}
