.cards-list-wrap {
  overflow: hidden;

  @media (max-width: 480px) {
    overflow: auto;
    width: calc(100% + 40px);
    margin-left: -20px;
  }
}

.cards-list {
  display: flex;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    width: 0;
    height: 0;
    background: transparent;
  }
}
